import { BaseAssetBalance } from './exchanges/ExchangeAssets';
import { AssetBalance } from 'binance-api-node';
import { Portfolio } from './services/portfolios/Portfolios';
import { Ticker } from '../io/exchange/Ticker';
import { AvailableExchanges } from './system/Portfolio';

export enum AppStatus {
  UNAUTHENTICATED = 'unauth',
  DATA_UNAVAILABLE = 'data-unavailable',
  DATA_LOADING = 'loading',
  IDLE = 'idle',
  CONFIGURE_ACCOUNT = 'configure-account'
}

export enum AppMode {
  STANDARD,
  PRO
}

/**
 * This is the central store object that the UI uses. All entities the UI interacts with must be 
 * stored here.
 */
export interface Inbest {

  /**
   * Defines the unique ID of the logged in user - i.e. sub
   */
  userId?: string;

  /**
   * Defines the current configuration and status of the app
   */
  config: InbestAppConfiguration;

  /**
   * Stores information about relevant exchanges
   */
  exchanges: { [key: string]: InbestExchangeConfiguration };

  /**
   * Stores the central information about Inbest.
   * * Connected portfolios
   * 
   */
  system: InbestSystem;
}

/**
 * Central information needed by the UI to interact with Inbest system
 */
export interface InbestSystem {

  /**
   * List of portfolios attached to the user
   */
  portfolios?: Portfolio[];
}

/**
 * Configuration about Inbest application, and current status
 */
export interface InbestAppConfiguration {
  /**
   * Current execution mode
   * Users can change execution mode to see features in different ways
   */
  mode: AppMode;

  /**
   * Current app status.
   * Status relates to authentication, availability of customers' data, and readiness of the application.
   */
  status: AppStatus;

  /**
   * Stores information about the health of the connected services
   */
  serviceHealth: { [key: string]: InbestServiceHealth };
}

/**
 * Stores public information about a connected exchange.
 * This information is updated via socket connections with the exchange's API.
 */
export interface InbestExchangeConfiguration {

  /**
   * Name of the exchange
   */
  exchangeName: string;

  /**
   * Current tickers for exchange's symbols
   */
  tickers: { [key: string]: Ticker };
}

// FIXME Is this needed?
export interface ExtendedAsset extends AssetBalance {
  $marketValue: number;
  $percentage: number;
  $assetType: number;
  $freeMarket: number;
  $lockedMarket: number;
}

/**
 * Defines the current health of a connected service
 */
export interface InbestServiceHealth {

  /**
   * Current service status
   */
  status: InbestServiceStatus;

  /**
   * Optional reason or explanation about the current status
   */
  message?: string;

  /**
   * Date this status became effective - formatted as YYYY-MM-DD HH:mm:ss
   */
  lastUpdate: string;
}

/**
 * Possible statuses of a connected service
 */
export enum InbestServiceStatus {

  /**
   * The service is operating as expected
   */
  OPERATIONAL       = 'operational',

  /**
   * The service is facing some issues and thus is operating with reduced capacity.
   * Potential delays may incur in communicating with services at reduced capacity.
   */
  REDUCED_CAPACITY  = 'reduced-capacity',

  // TODO Others
}