import { InbestStore } from './../io/InbestStore';
import { ExtendedAsset } from './../model/Inbest';
import Store from '@/store';
import { Ticker } from '@/io/exchange/Ticker';
import AssetDefinitions, { AssetType } from '@/model/ui/AssetDefinitions';

export default {
  getExtendedAssets (totalHoldings: number): ExtendedAsset[] {
    const state = (Store.state as InbestStore)
    const prices = state.portfolio!.prices;
    const assets = state.portfolio!.balances;

    const extendedAssets: ExtendedAsset[] = assets.map(asset => {
      const assetName = asset.asset;
      let marketPrice = 0;
      
      if (assetName === 'USDT') marketPrice = 1;
      else {
        const tickerSymbol = `${assetName}USDT`;
        const ticker = prices[tickerSymbol];
        if (!ticker) {
          const btcTicker = prices[`${assetName}BTC`];
          const btcUsdtTicker = prices[`BTCUSDT`];
          if (btcTicker && btcUsdtTicker) {
            marketPrice = btcTicker * btcUsdtTicker;
          }
        } else {
          marketPrice = ticker;
        }
      }

      const total = asset.total * marketPrice;
      const type = AssetDefinitions[assetName] ? AssetDefinitions[assetName].type : AssetType.VOLATILE;
      
      const ret: ExtendedAsset = {
        ...(asset as any).data,
        $marketValue: total,
        $percentage: total / totalHoldings,
        $assetType: type,
        $freeMarket: (asset as any).available * marketPrice,
        $lockedMarket: (asset as any).locked * marketPrice
      }

      return ret;
    });

    return extendedAssets;
  },

  getMarketValue (symbol: string) {
    if (symbol === 'USDT') return 1;
    const bid = parseFloat((Store.state.tickers.filter((ticker: Ticker) => ticker.symbol === `${symbol}USDT`)[0] || {}).bestBid);
    
    if (isNaN(bid)) {
      // No USDT market, try BTC
      const bid2 = parseFloat((Store.state.tickers.filter((ticker: Ticker) => ticker.symbol === `${symbol}BTC`)[0] || {}).bestBid);
      const btcBid = parseFloat((Store.state.tickers.filter((ticker: Ticker) => ticker.symbol === `BTCUSDT`)[0] || {}).bestBid);
      if (isNaN(bid2)) return 0;
      return bid2 * btcBid;
    }

    return bid;
  },

  getPortfolio (portfolioId: string) {
    const portfolio: Portfolio = Store.state.portfolios.filter((item: Portfolio) => item.id === portfolioId)[0];
    return portfolio;
  },

  mapTickerSymbol (value: any): Ticker {
    return new Ticker({
      eventType: value.e,
      eventTime: value.E,
      symbol: value.s,
      priceChange: parseFloat(value.p),
      priceChangePercent: parseFloat(value.P),
      weightedAvg: parseFloat(value.w),
      prevDayClose: parseFloat(value.x),
      curDayClose: parseFloat(value.c),
      closeTradeQuantity: parseFloat(value.Q),
      bestBid: parseFloat(value.b),
      bestBidQnt: parseFloat(value.B),
      bestAsk: parseFloat(value.a),
      bestAskQnt: parseFloat(value.A),
      open: parseFloat(value.o),
      high: parseFloat(value.h),
      low: parseFloat(value.l),
      volume: parseFloat(value.v),
      volumeQuote: parseFloat(value.q),
      openTime: value.O,
      closeTime: value.C,
      firstTradeId: value.F,
      lastTradeId: value.L,
      totalTrades: value.n
    })
  }
};