
export interface InbestRoute {
  path: string;
  name: string;
  component: any;
  meta: InbestRouteMetadata;
}

export interface InbestRouteMetadata {
  assets: InbestMetadataAssets;
  health: InbestMetadataHealth;
  operations: InbestMetadataOperations;
  style?: InbestMetadataStyle;
  fullApp?: boolean;
  submenu?: SubmenuItem[];
}

export interface SubmenuItem {
  label: string;
  href: string;
}

export enum AssetsVisibilityMode {
  COLLAPSED = 'collapsed',
  SUMMARY = 'summary',
  FULL = 'full'
}

export interface InbestMetadataAssets {
  mode: AssetsVisibilityMode;
  showMarketGraph?: boolean;
  showYieldGraph?: boolean;
  // TODO Others
}

export interface InbestMetadataOperations {
  mode: AssetsVisibilityMode;  
  // TODO Others
}

export interface InbestMetadataStyle {
  fullSize?: boolean;
  contentOverflow?: boolean;
}

export interface InbestMetadataHealth {
  mode: AssetsVisibilityMode;
  view?: string;
}