




























import { Vue, Component } from 'vue-property-decorator'
import { Inbest } from '../../model/Inbest';
import AssetDistribution from './AssetDistribution.vue';
import { Portfolio } from '../../model/services/portfolios/Portfolios';
import numeral from 'numeral';

@Component({
  components: {
    AssetDistribution
  },
  filters: {
    currencyValue (value: number) {
      return numeral(value).format('0,0.00');
    }
  }
})
export default class PortfolioList extends Vue {

  get portfolios () {
    return (this.$store.state as Inbest).system.portfolios;
  }

  getCurrencyIcon (name: string) {
    return `https://github.com/atomiclabs/cryptocurrency-icons/raw/master/32/color/${name.toLowerCase()}.png`
  }

  getExchangeIcon (name: string) {
    switch (name) {
      case 'paper':
        return '<i class="far fa-paper-plane"></i> Paper trading';
      default:
        return `<img class="exchange-logo" src="/media/exchanges/${name}.png" height="12" />`;
    }
  }

  getAssetValues (portfolio: Portfolio) {
    const exchangeName = portfolio.ExchangeName;
    const exchangeInfo = (this.$store.state as Inbest).exchanges[exchangeName];
    if (!exchangeInfo) {
      // TODO Throw exception
      return {}
    }
    const tickers = exchangeInfo.tickers;

    const baseCurrency = portfolio.BaseCurrency;
    const assetValues = portfolio.Assets.map(asset => {
      // Try directly from symbol to base currency
      const baseMarketName = `${asset.Symbol}${baseCurrency}`.toUpperCase();
      const baseMarket = tickers[baseMarketName];
      if (asset.Symbol === baseCurrency) {
        return { [asset.Symbol]: 1 };
      }
      
      if (!baseMarket) {
        // Base market unavailable.
        return { [asset.Symbol]: 0 }; // TODO
      }

      return { [asset.Symbol]: asset.Balance * baseMarket.bestBid };
    });

    return assetValues.reduce((total, item) => ({ ...total, ...item }), {});
  }

  getPortfolioValue (portfolio: Portfolio): number {
    const assetValues = this.getAssetValues(portfolio);

    const value = Object.values(assetValues).reduce((total, asset) => total + asset, 0);
    return value;
  }

  async mounted () {
    setInterval(() => this.$forceUpdate(), 5000);
  }
}
