






































































































































import { Vue, Component, Prop } from 'vue-property-decorator'
import ExchangeOperation from '../io/base/ExchangeOperation';
import Deal from '../io/autotrading/Deal';
import * as moment from 'moment';
import numeral from 'numeral';
import { DealStatus } from '../model/autotrading/Deal';
import { InbestStore } from '../io/InbestStore';
import Bot from '../io/autotrading/Bot';
import DoughnutChart from '@/components/graphs/DoughnutChart.vue';
import BarChart from '@/components/graphs/BarChart.vue';

@Component({
  components: {
    DoughnutChart,
    BarChart,
  },
  filters: {
    currencyValue (value: number) {
      return numeral(value).format('0,0.00');
    },
    dealDuration (deal: ExchangeOperation) {
      const end = deal.operationEndDate.getTime();
      const start = deal.operationStartDate.getTime();
      const diff = (end - start);

      return Date.now() - diff;
    },
    dealsYield (deals: Deal[]) {
      const profits = deals.map(deal => deal.usd_final_profit);
      return profits.reduce((t, i) => t + i, 0);
    },
    fromNow (value: Date) {
      return moment(value).fromNow();
    },
    fromNowShort (value: Date) {
      const d = moment(value).fromNow().split(' ');
      d.pop();
      return d.join(' ');
    },
    numPages (value: ExchangeOperation[]) {
      const totalPages = value.length / 10;
      const rem = value.length % 10;

      const ret = Math.floor(totalPages) + (rem !== 0 ? 1 : 0);
      return ret;
    }
  }
})
export default class BotDetailStats extends Vue {
  @Prop(Bot) readonly bot!: Bot;

  get state () {
    return (this.$store.state as InbestStore);
  }

  get deals () {
    return this.state.autotrading.deals;
  }

  get botDeals () {
    return this.deals.filter(d => d.bot_id === this.bot.id);
  }

  get botDailyOperations () {
    const minusOneDay = Date.now() - 86400000;
    return this.botDeals.filter(d => d.operationEndDate.getTime() > minusOneDay );
  }

  get activeDeals () {
    return this.botDeals.filter(d => [DealStatus.BOUGHT, DealStatus.TRAILING].indexOf(d.status) !== -1);
  }

  get finishedDeals () {
    return this.botDeals.filter(d => [DealStatus.COMPLETED, DealStatus.TAKE_PROFIT_FINISHED, DealStatus.PANIC_SELL_FINISHED, DealStatus.STOP_LOSS_FINISHED].indexOf(d.status) !== -1);
  }

  get chartOptions () {
    return {
      responsive: true,
      maintainAspectRatio: false,
      legend: {
        display: false
      }
    }
  }

  get dealsStartedByDay () {
    const deals = this.botDeals.reduce((t, i) => ({
      ...t,
      [moment(i.operationStartDate).format('YYYY-MM-DD')]: (t[moment(i.operationStartDate).format('YYYY-MM-DD')] || []).concat([i])
    }), {});

    const numDays = Object.keys(deals).length;
    const rawValues = Object.values(deals).map((d: any[]) => d.length);

    return numeral(rawValues.reduce((t, i) => t + i, 0) / numDays).format('0.0');
  }

  get dealsFinishedByDay () {
    const deals = this.finishedDeals.reduce((t, i) => ({
      ...t,
      [moment(i.operationStartDate).format('YYYY-MM-DD')]: (t[moment(i.operationStartDate).format('YYYY-MM-DD')] || []).concat([i])
    }), {});

    const numDays = Object.keys(deals).length;
    const rawValues = Object.values(deals).map((d: any[]) => d.length);

    return numeral(rawValues.reduce((t, i) => t + i, 0) / numDays).format('0.0');
  }

  get averageTimeToProfit () {
    const deals = this.finishedDeals.reduce((t, i) => ({
      ...t,
      [Math.round((i.operationEndDate.getTime() - i.operationStartDate.getTime()) / 3600000)]: (t[Math.round((i.operationEndDate.getTime() - i.operationStartDate.getTime()) / 3600000)] || []).concat([i])
    }), {});

    const numDays = Object.keys(deals).length;
    const rawValues = Object.values(deals).map((d: any[]) => d.length);

    return numeral(rawValues.reduce((t, i) => t + i, 0) / numDays).format('0.0');
  }

  get shortestDealDuration () {
    const deals = this.finishedDeals.reduce((t, i) => ({
      ...t,
      [Math.round((i.operationEndDate.getTime() - i.operationStartDate.getTime()) / 60000)]: (t[Math.round((i.operationEndDate.getTime() - i.operationStartDate.getTime()) / 60000)] || []).concat([i])
    }), {});

    const times = Object.keys(deals);
    const shortest = times.sort((a, b) => parseInt(b) < parseInt(a) ? 1 : -1)[0];

    return shortest
  }

  get longestActiveDeal () {
    const deal = this.activeDeals.sort((a, b) => b.operationStartDate < a.operationStartDate ? 1 : -1)[0]
    if (!deal) return {};
    return deal;
  }

  get longestClosedDeal () {
    const deal = this.finishedDeals.sort((a, b) => (b.operationEndDate.getTime() - b.operationStartDate.getTime()) > (a.operationEndDate.getTime() - a.operationStartDate.getTime()) ? 1 : -1)[0]
    return deal;
  }

  get averageInvestmentPerDeal () {
    const deals = this.finishedDeals.map(d => d.bought_volume)
    return numeral(deals.reduce((t, i) => t + i, 0) / deals.length).format('0,0.00[000]');
  }

  get averageInvestmentPerDay () {
    const deals = this.finishedDeals.reduce((t, i) => ({
      ...t,
      [moment(i.operationStartDate).format('YYYY-MM-DD')]: ((t[moment(i.operationStartDate).format('YYYY-MM-DD')] || []).concat([i.bought_volume]))
    }), {});
    
    const avgDays = Object.values(deals).map((d: number[]) => d.reduce((t, i) => t + i, 0));
    const total: number = avgDays.reduce((t: number, i: number) => t + i, 0)

    return numeral(total / avgDays.length).format('0,0.00[000]');
  }

  get totalInvestment () {
    const deals = this.finishedDeals.map(d => d.bought_volume)
    return numeral(deals.reduce((t, i) => t + i, 0)).format('0,0.00[000]');
  }

  get averageProfitPerDeal () {
    const deals = this.finishedDeals.map(d => d.final_profit_percentage)
    return numeral(deals.reduce((t, i) => t + i, 0) / deals.length).format('0.00');
  }

  get averageProfitPerDay () {
    const deals = this.finishedDeals.reduce((t, i) => ({
      ...t,
      [moment(i.operationStartDate).format('YYYY-MM-DD')]: ((t[moment(i.operationStartDate).format('YYYY-MM-DD')] || []).concat([i.final_profit_percentage]))
    }), {});
    
    const avgDays = Object.values(deals).map((d: number[]) => d.reduce((t, i) => t + i, 0) / d.length);
    const total: number = avgDays.reduce((t: number, i: number) => t + i, 0)

    return numeral(total / avgDays.length).format('0.00');
  }

  get averageTotalProfit () {
    const deals = this.finishedDeals
      .map(d => d.sold_volume)
      .reduce((t, i) => t + i, 0)
    const investment = this.finishedDeals
      .map(d => d.bought_volume)
      .reduce((t, i) => t + i, 0)

    const result = (deals / investment) - 1.00;
    return numeral(result * 100).format('0.00');
  }

  get investmentPerDayChart () {
    const deals = this.finishedDeals.reduce((t, i) => ({
      ...t,
      [moment(i.operationStartDate).format('YYYY-MM-DD')]: ((t[moment(i.operationStartDate).format('YYYY-MM-DD')] || []).concat([i.bought_volume]))
    }), {});

    const sorted = Object.keys(deals).sort();
    const labels = sorted.map(d => moment(d).format('MMM DD'));
    const data = sorted.map(key => deals[key].reduce((t, i) => t + i, 0))

    return {
      labels,
      datasets: [{
        backgroundColor: '#01acfd',
        data
      }]
    }
  }

  get profitPerDayChart () {
    const deals = this.finishedDeals.reduce((t, i) => ({
      ...t,
      [moment(i.operationStartDate).format('YYYY-MM-DD')]: ((t[moment(i.operationStartDate).format('YYYY-MM-DD')] || []).concat([i.actual_usd_profit]))
    }), {});

    const sorted = Object.keys(deals).sort();
    const labels = sorted.map(d => moment(d).format('MMM DD'));
    const data = sorted.map(key => deals[key].reduce((t, i) => t + i, 0))

    return {
      labels,
      datasets: [{
        backgroundColor: '#01acfd',
        data
      }]
    }
  }

  get profitPerPairChart () {
    const deals = this.finishedDeals.reduce((t, i) => ({
      ...t,
      [i.pair]: ((t[i.pair] || []).concat([i.actual_usd_profit]))
    }), {});

    const sumByPair = Object.values(deals).map((d: number[]) => d.reduce((t, i) => t + i, 0));

    return {
      labels: Object.keys(deals),
      datasets: [{
        backgroundColor: '#01acfd',
        data: sumByPair
      }]
    }
  }

  async created () {}

  async mounted () {}
}
