






import { Vue, Component, Prop } from 'vue-property-decorator'
import { Inbest } from '../../model/Inbest';
import DoughnutChart from '../graphs/DoughnutChart.vue';
import AssetDefinitions from '@/model/ui/AssetDefinitions';
import randomcolor from 'randomcolor';

@Component({
  components: {
    DoughnutChart
  }
})
export default class AssetDistribution extends Vue {
  @Prop(Array) readonly portfolioIds!: string[];
  @Prop(Object) readonly assetValues!: { [key: string]: number };

  get portfolios () {
    return (this.$store.state as Inbest).system.portfolios
      .filter(portfolio => this.portfolioIds.indexOf(portfolio.PortfolioId) !== -1);
  }

  get assets () {
    // @ts-ignore
    const assets = this.portfolios.map(portfolio => portfolio.Assets).flat()
      .reduce((total, item) => ({
        ...total,
        [item.Symbol]: (total[item.Symbol] || 0) + (item.Balance * this.assetValues[item.Symbol] || 0)
      }), {});
    return assets;
  }

  get chartData () {
    const assets = this.assets;

    return {
      labels: Object.keys(assets),
      datasets: [{
        borderWidth: 0,
        backgroundColor: Object.keys(assets).map(a => (AssetDefinitions[a] || {}).color || randomcolor()),
        data: Object.values(assets)
      }]
    }
  }

  get marketTickers () {
    return (this.$store.state as Inbest);
  }

  async created () {}

  async mounted () {}
}
