import { InbestStore } from './../../io/InbestStore';
import { AggregatedPortfolio } from './../../io/base/AggregatedPortfolio';
import { Inbest, AppStatus, AppMode } from './../../model/Inbest';

export default {

  CONFIGURE_AGGREGATED_PORTFOLIO (state: InbestStore, portfolio: AggregatedPortfolio) {
    state.portfolio = portfolio;
  },

  UPDATE_APP_MODE (state: Inbest, mode: AppMode) {
    state.config.mode = mode;
  },

  UPDATE_APP_STATUS (state: Inbest, status: AppStatus) {
    state.config.status = status;
  },

  UPDATE_USER_ID (state: Inbest, userId?: string) {
    if (!userId) {
      state.config.status = AppStatus.UNAUTHENTICATED;
    } else {
      state.config.status = AppStatus.IDLE;
    }

    state.userId = userId;
  }
}