





























import { Vue, Component } from 'vue-property-decorator'
import BalanceOverview from '@/components/BalanceOverview.vue';
import ExchangeOverview from '@/components/ExchangeOverview.vue';
import AssetOverviewList from '@/components/AssetOverviewList.vue';
import TradingViews from '@/components/TradingViews.vue';
import AssetHealth from '@/components/AssetHealth.vue';
import { InbestStore } from '../io/InbestStore';
import { NoCache } from '../decorators';
import { InbestTradingCircle } from '../io/ui/TradingCircle';
import { TradeStatus } from '../model/ui/TradingCircle';
import AppMenu from '@/components/AppMenu.vue';
import NewsFlashList from '@/components/NewsFlashList.vue';
import { InbestRoute } from '../model/ui/SystemDefinitions';
import MarketOverview from '@/components/MarketOverview.vue';

@Component({
  components: {
    AppMenu,
    AssetHealth,
    AssetOverviewList,
    BalanceOverview,
    ExchangeOverview,
    MarketOverview,
    NewsFlashList,
    TradingViews,
  }
})
export default class InbestView extends Vue {

  get contentOverflow () {
    const route = (this.$route as any as InbestRoute);
    return route.meta.style && route.meta.style.contentOverflow;
  }

  get contentFullSize () {
    const route = (this.$route as any as InbestRoute);
    return route.meta.style && route.meta.style.fullSize;
  }

  @NoCache
  get portfolio () {
    return (this.$store.state as InbestStore).portfolio;
  }

  get assetsVisibilityMode () {
    return (this.$route as any as InbestRoute).meta.assets.mode;
  }

  get healthHookView () {
    return (this.$route as any as InbestRoute).meta.health.view;
  }

  get healthVisibilityMode () {
    return (this.$route as any as InbestRoute).meta.health.mode;
  }

  get operationsVisibilityMode () {
    return (this.$route as any as InbestRoute).meta.operations.mode;
  }

  async created () {}

  async mounted () {}
}
