import { TradeStatus } from './../../model/ui/TradingCircle';
import { OperationMethod, OperationType, SmartTradeTakeProfitType, SmartTradeStatus, SmartTradeStepType } from './../../model/autotrading/Trade';
import { DealStatus, DealStopLossType } from './../../model/autotrading/Deal';
import { Trade as TradeProps, SmartTradeType, TradeStep as TradeStepProps } from '../../model/autotrading/Trade';
import ExchangeOperation from '../base/ExchangeOperation';
import { TradeStrategy } from '@/model/ui/TradingCircle';

export default class Trade extends ExchangeOperation implements TradeProps {
  private readonly data: TradeProps;

  constructor (data: TradeProps) {
    super();
    this.data = {
      ...data,
      take_profit_price_condition: parseFloat(data.take_profit_price_condition as any),
      stop_loss_price_condition: parseFloat(data.stop_loss_price_condition as any),
      units_to_buy: parseFloat(data.units_to_buy as any),
      buy_price: parseFloat(data.buy_price as any),
      total: parseFloat(data.total as any),
      average_buy_price: parseFloat(data.average_buy_price as any),
      trailing_take_profit_step: parseFloat(data.trailing_take_profit_step as any),
      take_profit_sell_order_price: parseFloat(data.take_profit_sell_order_price as any),
      stop_loss_sell_order_price: parseFloat(data.stop_loss_sell_order_price as any),
      trailing_buy_step: parseFloat(data.trailing_buy_step as any),
      profit_percentage: parseFloat(data.profit_percentage as any),
      current_price: parseFloat(data.current_price as any),
      usd_profit: parseFloat(data.usd_profit as any),
    }
  }

  /*
   * ExchangeOperation props
   */

  get operationStrategy (): TradeStrategy {
    return TradeStrategy.LONG
  }

  get operationStatus (): TradeStatus {
    switch (this.data.status) {
      case DealStatus.TRAILING_BUY_ACTIVATED:
        return TradeStatus.AWAITING;
      case DealStatus.CANCELLED:
        return TradeStatus.CANCELLED;
      case DealStatus.BOUGHT:
        return TradeStatus.EXECUTING;
      case DealStatus.TRAILING:
        return TradeStatus.TRAILING;
      case DealStatus.TAKE_PROFIT_FINISHED:
      case DealStatus.COMPLETED:
      case DealStatus.TRADE_FINISHED:
      case DealStatus.PANIC_SELL_FINISHED:
      case DealStatus.FAILED:
      case DealStatus.STOP_LOSS_FINISHED:
        return TradeStatus.FINISHED;
      default:
        return TradeStatus.AWAITING;
    }

  }

  get operationBaseCurrency (): string {
    return this.from_currency_code;
  }

  get operationBaseAmount (): number {
    return this.buy_price;
  }

  get operationQuoteCurrency (): string {
    return this.to_currency_code;
  }

  get operationQuoteAmount (): number {
    return this.units_to_buy;
  }

  get operationCurrentProfit (): number {
    return this.profit_percentage;
  }

  get operationTargetProfit (): number {
    return 3;
  }

  get operationStartDate (): Date {
    return new Date(this.created_at);
  }

  get operationStartValue (): number {
    return 0;
  }

  get operationEndDate (): Date {
    return new Date(this.closed_at!);
  }

  get operationEndValue (): number {
    return 0;
  }

  get operationTargetValue (): number {
    return 0;
  }

  get operationTrailingValue (): number {
    return 0;
  }


  /* 
   * Autogenerated props
   */

  get id (): number {
    return this.data.id;
  }

  get account_id (): number {
    return this.data.account_id;
  }

  get trailing_stop_loss (): boolean {
    return this.data.trailing_stop_loss;
  }

  get from_currency_id (): number {
    return this.data.from_currency_id;
  }

  get trailing_stop_loss_condition_percentage (): null {
    return this.data.trailing_stop_loss_condition_percentage;
  }

  get trailing_take_profit (): boolean {
    return this.data.trailing_take_profit;
  }

  get stop_loss_enabled (): boolean {
    return this.data.stop_loss_enabled;
  }

  get panic_sell_market_order_id (): null {
    return this.data.panic_sell_market_order_id;
  }

  get take_profit_enabled (): boolean {
    return this.data.take_profit_enabled;
  }

  get trailing_buy_enabled (): boolean {
    return this.data.trailing_buy_enabled;
  }

  get stop_loss_timeout_seconds (): number {
    return this.data.stop_loss_timeout_seconds;
  }

  get stop_loss_timeout_enabled (): boolean {
    return this.data.stop_loss_timeout_enabled;
  }

  get panic_sellable (): boolean {
    return this.data.panic_sellable;
  }

  get editable (): boolean {
    return this.data.editable;
  }

  get finished (): boolean {
    return this.data.finished;
  }

  get cancellable (): boolean {
    return this.data.cancellable;
  }

  get created_at (): Date {
    return this.data.created_at;
  }

  get updated_at (): Date {
    return this.data.updated_at;
  }

  get trailing_stop_loss_last_price_updated_at (): null {
    return this.data.trailing_stop_loss_last_price_updated_at;
  }

  get trailing_take_profit_last_price_updated_at (): null {
    return this.data.trailing_take_profit_last_price_updated_at;
  }

  get closed_at (): null | number {
    return this.data.closed_at;
  }

  get status (): DealStatus {
    return this.data.status;
  }

  get take_profit_price_condition (): number {
    return this.data.take_profit_price_condition;
  }

  get take_profit_price_order (): null {
    return this.data.take_profit_price_order;
  }

  get stop_loss_price_condition (): number {
    return this.data.stop_loss_price_condition;
  }

  get stop_loss_price_order (): null {
    return this.data.stop_loss_price_order;
  }

  get units_to_buy (): number {
    return this.data.units_to_buy;
  }

  get buy_price (): number {
    return this.data.buy_price;
  }

  get total (): number {
    return this.data.total;
  }

  get average_buy_price (): number {
    return this.data.average_buy_price;
  }

  get average_sell_price (): null {
    return this.data.average_sell_price;
  }

  get error_message (): null {
    return this.data.error_message;
  }

  get trailing_take_profit_step (): number {
    return this.data.trailing_take_profit_step;
  }

  get type (): SmartTradeType {
    return this.data.type;
  }

  get stop_loss_price_method (): OperationMethod {
    return this.data.stop_loss_price_method;
  }

  get take_profit_price_method (): OperationMethod {
    return this.data.take_profit_price_method;
  }

  get trailing_stop_loss_last_price (): null {
    return this.data.trailing_stop_loss_last_price;
  }

  get trailing_take_profit_last_price (): null {
    return this.data.trailing_take_profit_last_price;
  }

  get note (): string {
    return this.data.note;
  }

  get buy_method (): OperationType {
    return this.data.buy_method;
  }

  get take_profit_type (): SmartTradeTakeProfitType {
    return this.data.take_profit_type;
  }

  get stop_loss_type (): DealStopLossType {
    return this.data.stop_loss_type;
  }

  get stop_loss_sell_method (): OperationType {
    return this.data.stop_loss_sell_method;
  }

  get take_profit_sell_method (): OperationType {
    return this.data.take_profit_sell_method;
  }

  get take_profit_sell_order_price (): number {
    return this.data.take_profit_sell_order_price;
  }

  get stop_loss_sell_order_price (): number {
    return this.data.stop_loss_sell_order_price;
  }

  get trailing_buy_step (): number {
    return this.data.trailing_buy_step;
  }

  get profit_percentage (): number {
    return this.data.profit_percentage;
  }

  get current_price (): number {
    return this.data.current_price;
  }

  get account_market (): string {
    return this.data.account_market;
  }

  get account_name (): string {
    return this.data.account_name;
  }

  get from_currency_code (): string {
    return this.data.from_currency_code;
  }

  get from_currency_name (): string {
    return this.data.from_currency_name;
  }

  get from_currency_display_name (): string {
    return this.data.from_currency_display_name;
  }

  get to_currency_code (): string {
    return this.data.to_currency_code;
  }

  get to_currency_name (): string {
    return this.data.to_currency_name;
  }

  get to_currency_display_name (): string {
    return this.data.to_currency_display_name;
  }

  get usd_profit (): number {
    return this.data.usd_profit;
  }

  get smart_trade_steps (): TradeStep[] {
    return this.data.smart_trade_steps.map(item => new TradeStep(item));
  }

  get take_profit_steps (): TradeStep[] {
    return this.data.take_profit_steps.map(item => new TradeStep(item));
  }

  get stop_loss_steps (): TradeStep[] {
    return this.data.stop_loss_steps.map(item => new TradeStep(item));
  }

  get buy_steps (): TradeStep[] {
    return this.data.buy_steps.map(item => new TradeStep(item));
  }

}

export class TradeStep implements TradeStepProps {

  private readonly data: TradeStepProps;

  constructor (data: TradeStepProps) {
    this.data = {
      ...data,
      actual_amount: parseFloat(data.actual_amount as any),
      actual_total: parseFloat(data.actual_total as any),
      average_result_price: parseFloat(data.average_result_price as any),
      initial_amount: parseFloat(data.initial_amount as any),
      initial_total: parseFloat(data.initial_total as any),
      price: parseFloat(data.price as any),
      trailing_last_pirce: parseFloat(data.trailing_last_pirce as any),
    }
  }

  get actual_amount (): number {
    return this.data.actual_amount!;
  }

  get actual_total (): number {
    return this.data.actual_total;
  }

  get average_result_price (): number {
    return this.data.average_result_price;
  }

  get created_at (): Date {
    return this.data.created_at;
  }

  get deviation_condition (): null {
    return this.data.deviation_condition;
  }

  get editable (): boolean {
    return this.data.editable;
  }

  get id (): number {
    return this.data.id;
  }

  get initial_amount (): number {
    return this.data.initial_amount;
  }

  get initial_total (): number {
    return this.data.initial_total;
  }

  get is_panic_sell (): boolean {
    return this.data.is_panic_sell!;
  }

  get market_class (): string {
    return this.data.market_class;
  }

  get market_order_id (): string {
    return this.data.market_order_id;
  }

  get order_id (): number {
    return this.data.order_id!;
  }

  get pair (): string {
    return this.data.pair;
  }

  get percent (): number {
    return this.data.percent!;
  }

  get position (): number {
    return this.data.position!;
  }

  get price_method (): OperationMethod {
    return this.data.price_method;
  }

  get price_percentage (): number {
    return this.data.price_percentage!;
  }

  get price (): number {
    return this.data.price;
  }

  get sell_method (): OperationType {
    return this.data.sell_method;
  }

  get sell_order_price (): number {
    return this.data.sell_order_price!;
  }

  get smart_trade_id (): number {
    return this.data.smart_trade_id;
  }

  get status (): SmartTradeStatus {
    return this.data.status;
  }

  get trailing_last_pirce_updated_at (): Date {
    return this.data.trailing_last_pirce_updated_at!;
  }

  get trailing_last_pirce (): number {
    return this.data.trailing_last_pirce!;
  }

  get trailing_last_price_updated_at (): Date {
    return this.data.trailing_last_price_updated_at!;
  }

  get trailing_last_price (): null {
    return this.data.trailing_last_price;
  }

  get type (): SmartTradeStepType {
    return this.data.type;
  }

  get updated_at (): Date {
    return this.data.updated_at;
  }

}