import ThirdPartyApiService from '@/services/ThirdPartyApiService';
import Bot from '@/io/autotrading/Bot';
import Deal from '@/io/autotrading/Deal';
import Trade from '@/io/autotrading/Trade';

const thirdPartyApi = ThirdPartyApiService.getInstance();

export default {

  async GET_BOTS (context: any, portfolioIds: string[]) {
    const bots = await Promise.all(portfolioIds.map(async portfolioId => {
      return await thirdPartyApi.autotradingBots(portfolioId);
    }))

    context.commit('UPDATE_BOTS', bots.flat().map(d => new Bot(d)));
  },

  async GET_DEALS (context: any, portfolioIds: string[]) {
    const deals = await Promise.all(portfolioIds.map(async portfolioId => {
      return await thirdPartyApi.autotradingDeals(portfolioId);
    }))

    context.commit('UPDATE_DEALS', deals.flat().map(d => new Deal(d)));
  },

  async GET_TRADES (context: any, portfolioIds: string[]) {
    const trades = await Promise.all(portfolioIds.map(async portfolioId => {
      return await thirdPartyApi.autotradingTrades(portfolioId);
    }));

    context.commit('UPDATE_TRADES', trades.flat().map(t => new Trade(t)));
  }
}