

















































import { Vue, Component } from 'vue-property-decorator'
import Blockly from 'blockly';
import { Blocks, Styles } from '../model/ui/blockly';

@Component
export default class IntelligenceView extends Vue {

  private readonly blocklyTheme = new Blockly.Theme('Inbest', (Styles.blockStyles as any), Styles.categoryStyles);

  async created () {
    const allBlocks = Blocks;
    Object.keys(allBlocks).map(key => {
      const block = allBlocks[key];
      Blockly.Blocks[key] = {
        init () {
          this.jsonInit(block);
        }
      }
    })
  }

  async mounted () {

    this.blocklyTheme.setComponentStyle('toolboxBackgroundColour', 'transparent');
    this.blocklyTheme.setComponentStyle('workspaceBackgroundColour', 'transparent');
    this.blocklyTheme.setComponentStyle('toolboxForegroundColour', 'white');
    this.blocklyTheme.setComponentStyle('flyoutBackgroundColour', 'transparent');
    this.blocklyTheme.setComponentStyle('scrollbarOpacity', '0');

    Blockly.inject('blocklyDiv', {
      toolbox: document.getElementById('toolbox'),
      theme: this.blocklyTheme
    });
  }
}
