import { Ticker } from './../../io/exchange/Ticker';
import BinanceService from '../../services/exchanges/BinanceService';
import * as util from '@/util';
import { AvailableExchanges } from '../../model/system/Portfolio';

export interface RawTickersUpdateRequest {
  data: any;
  exchangeName: string;
}

export interface TickersUpdateRequest {
  tickers: Ticker[];
  exchangeName: string;
}

const binanceService = BinanceService.getInstance();
export default {
  async START_TICKER_CONNECTION (_: any, exchangeName: string) {
    switch (exchangeName) {
      case AvailableExchanges.BINANCE:
        await binanceService.startTickerSubscription();
        break;
      default:
        console.error(`Unknown exchange ${exchangeName}`);
        // TODO Throw
    }
  },

  RAW_TICKERS_UPDATED (context: any, data: RawTickersUpdateRequest) {
    switch(data.exchangeName) {
      case AvailableExchanges.BINANCE:
        const parsedTickers = data.data.map((i: any) => util.Binance.mapTickerSymbol(i));
        context.dispatch('TICKERS_UPDATED', { 
          exchangeName: data.exchangeName,
          tickers: parsedTickers
        });
        break;
      default:
        console.error(`Unknown exchange ${data.exchangeName}`);
        // TODO Throw
    }
  },

  TICKERS_UPDATED (context: any, data: TickersUpdateRequest[]) {
    context.commit('UPDATE_TICKERS', data);
  }
}