import { Portfolio } from './../model/system/Portfolio';
import { DynamoDB } from 'aws-sdk';
import Amplify from 'aws-amplify';
import ConfigurationService from './ConfigurationService';
import Store from '@/store';

// Imports

export default class InbestDataService {

  private static instance: InbestDataService;

  private readonly config: ConfigurationService = ConfigurationService.getInstance();

  private ddb!: DynamoDB.DocumentClient;
  private readonly portfoliosTableName!: string;
  private readonly assetsTableName!: string;
  private readonly balancesTableName!: string;

  constructor () {
    this.portfoliosTableName = this.config.get('PORTFOLIOS_TABLE_NAME');
    this.assetsTableName = this.config.get('ASSETS_TABLE_NAME');
    this.balancesTableName = this.config.get('BALANCES_TABLE_NAME');
  }

  private async configure () {
    const credentials = await Amplify.Auth.currentUserCredentials();
    const region = this.config.get('AWS_REGION');
    this.ddb = new DynamoDB.DocumentClient({ credentials, region });
  }

  async portfolios (): Promise<Portfolio[]> {
    if (!this.ddb) await this.configure();
    const response = await this.ddb.query({
      TableName: this.portfoliosTableName,
      KeyConditionExpression: 'UserId = :user',
      ExpressionAttributeValues: {
        ':user': Store.state.userId
      }
    }).promise();

    const items = response.Items!;
    return items;
  }

  async assets () {

  }

  async balances () {

  }

  static getInstance () {
    if (!InbestDataService.instance) InbestDataService.instance = new InbestDataService()
    return InbestDataService.instance
  }
}
