



































import { Vue, Component, Prop } from 'vue-property-decorator'
import { Asset } from '../io/exchange/Asset';
import { Ticker } from '../io/exchange/Ticker';
import { InbestStore } from '../io/InbestStore';
import SymbolTradingView from '@/components/SymbolTradingView.vue';
import * as util from '@/util';
import numeral from 'numeral';
import { AssetsVisibilityMode } from '../model/ui/SystemDefinitions';

@Component({
  components: {
    SymbolTradingView
  },
  filters: {
    assetIcon (asset: string) {
      return `https://github.com/atomiclabs/cryptocurrency-icons/raw/master/32/color/${asset.toLowerCase()}.png`
    },
    maxPrecision (value: number) {
      return numeral(value).format('0,0.00000');
    },
    percent (value: number) {
      return util.Numbers.percent(value);
    },
    shortNumber (value: number) {
      return util.Numbers.shortNumber(value);
    }
  }
})
export default class AssetOverview extends Vue {
  @Prop(Asset) readonly asset!: Asset;
  @Prop(Number) readonly price!: number;
  @Prop(Number) readonly percentage!: number;
  @Prop(Number) readonly totalHoldings!: number;
  @Prop(String) readonly mode!: AssetsVisibilityMode;

  get marketPrice () {
    if (!this.price) return 0;
    return this.asset.total * this.price;
  }

  get portfolio () {
    return (this.$store.state as InbestStore).portfolio!;
  }

  async created () {}

  async mounted () {

  }
}
