import { InbestRoute, AssetsVisibilityMode } from './../model/ui/SystemDefinitions';
import Vue from 'vue'
import VueRouter from 'vue-router'

import BalancesView from '@/views/BalancesView.vue';
import Home from '@/views/Home.vue'
import Trading from '@/views/Trading.vue';
import Intelligence from '@/views/Intelligence.vue';
import Bots from '@/views/Bots.vue';
import BotZoom from '@/views/BotZoom.vue';
import PortfolioInitialSetup from '@/views/PortfolioInitialSetup.vue';
import PortfolioSettings from '../views/PortfolioSettings.vue';
import TradingConsole from '../views/TradingConsole.vue';

Vue.use(VueRouter)

const routes: InbestRoute[] = [
  {
    path: '/portfolios-start',
    name: 'portfolios-start',
    component: PortfolioInitialSetup,
    meta: {
      assets: {
        mode: AssetsVisibilityMode.COLLAPSED
      },
      operations: {
        mode: AssetsVisibilityMode.COLLAPSED
      },
      health: {
        mode: AssetsVisibilityMode.COLLAPSED
      }
    }
  },
  {
    path: '/portfolios/:portfolioId/settings',
    name: 'portfolio-settings',
    component: PortfolioSettings,
    meta: {
      assets: {
        mode: AssetsVisibilityMode.COLLAPSED
      },
      operations: {
        mode: AssetsVisibilityMode.COLLAPSED
      },
      health: {
        mode: AssetsVisibilityMode.COLLAPSED
      }
    }
  },
  {
    path: '/',
    name: 'home',
    component: Home,
    meta: {
      assets: {
        mode: AssetsVisibilityMode.SUMMARY,
        showMarketGraph: true
      },
      operations: {
        mode: AssetsVisibilityMode.SUMMARY
      },
      health: {
        mode: AssetsVisibilityMode.SUMMARY
      }
    }
  },
  {
    path: '/trade',
    name: 'trading',
    component: TradingConsole,
    meta: {
      assets: {
        mode: AssetsVisibilityMode.SUMMARY,
        showMarketGraph: false
      },
      operations: {
        mode: AssetsVisibilityMode.SUMMARY
      },
      health: {
        mode: AssetsVisibilityMode.SUMMARY
      },
      submenu: [
        {
          label: 'Console',
          href: '/trade'
        },
        {
          label: 'My trades',
          href: '/trade/trades'
        },
        {
          label: 'Markets',
          href: '/trade/markets'
        },
        {
          label: 'Signals',
          href: '/trade/signals'
        },
      ]
    }
  },
  {
    path: '/intelligence',
    name: 'intelligence',
    component: Intelligence,
    meta: {
      assets: {
        mode: AssetsVisibilityMode.COLLAPSED
      },
      operations: {
        mode: AssetsVisibilityMode.COLLAPSED
      },
      health: {
        mode: AssetsVisibilityMode.SUMMARY,
        view: 'market'
      }
    }
  },
  {
    path: '/balances',
    name: 'balances',
    component: BalancesView,
    meta: {
      assets: {
        mode: AssetsVisibilityMode.FULL,
        showMarketGraph: false
      },
      operations: {
        mode: AssetsVisibilityMode.COLLAPSED
      },
      health: {
        mode: AssetsVisibilityMode.SUMMARY
      }
    }
  },
  {
    path: '/bots',
    name: 'bots',
    component: Bots,
    meta: {
      assets: {
        mode: AssetsVisibilityMode.COLLAPSED,
        showMarketGraph: false
      },
      operations: {
        mode: AssetsVisibilityMode.COLLAPSED
      },
      style: {
        fullSize: true,
        contentOverflow: true
      },
      health: {
        mode: AssetsVisibilityMode.SUMMARY,
        view: 'market'
      }
    }
  },
  {
    path: '/bots/:botId/zoom',
    name: 'bot-zoom',
    component: BotZoom,
    meta: {
      assets: {
        mode: AssetsVisibilityMode.COLLAPSED,
        showMarketGraph: false
      },
      operations: {
        mode: AssetsVisibilityMode.COLLAPSED
      },
      style: {
        fullSize: false,
        contentOverflow: true
      },
      health: {
        mode: AssetsVisibilityMode.SUMMARY,
        view: 'market'
      }
    }
  },
  // {
  //   path: '/operations',
  //   name: 'operations',
  //   component: Operations
  // },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
