import { BaseDataWithHistory } from './../base/BaseDataWithHistory';
import { BinanceTicker } from './../../model/exchanges/binance/BinanceExchangeAssets';

export class Ticker extends BaseDataWithHistory<BinanceTicker> implements BinanceTicker {

  get eventType (): string {
    return this.data.eventType;
  }

  get eventTime (): Date {
    return this.data.eventTime;
  }

  get symbol (): string {
    return this.data.symbol;
  }

  get priceChange (): number {
    return this.data.priceChange;
  }

  get priceChangePercent (): number {
    return this.data.priceChangePercent;
  }

  get weightedAvg (): number {
    return this.data.weightedAvg;
  }

  get prevDayClose (): number {
    return this.data.prevDayClose;
  }

  get curDayClose (): number {
    return this.data.curDayClose;
  }

  get closeTradeQuantity (): number {
    return this.data.closeTradeQuantity;
  }

  get bestBid (): number {
    return this.data.bestBid;
  }

  get bestBidQnt (): number {
    return this.data.bestBidQnt;
  }

  get bestAsk (): number {
    return this.data.bestAsk;
  }

  get bestAskQnt (): number {
    return this.data.bestAskQnt;
  }

  get open (): number {
    return this.data.open;
  }

  get high (): number {
    return this.data.high;
  }

  get low (): number {
    return this.data.low;
  }

  get volume (): number {
    return this.data.volume;
  }

  get volumeQuote (): number {
    return this.data.volumeQuote;
  }

  get openTime (): number {
    return this.data.openTime;
  }

  get closeTime (): number {
    return this.data.closeTime;
  }

  get firstTradeId (): number {
    return this.data.firstTradeId;
  }

  get lastTradeId (): number {
    return this.data.lastTradeId;
  }

  get totalTrades (): number {
    return this.data.totalTrades;
  }
}