import Store from '@/store';
import * as util from '@/util';

export default class SocketService {
  private static instance: SocketService
  public readonly socket: any;

  private readonly subscriptions: Function[] = [];
  private connection: any = null;

  subscribe (handler: Function) {
    this.subscriptions.push(handler);
  }

  async connect () {
    if (this.connection) return this;
    return await new Promise((resolve, reject) => {
      const socket = new WebSocket('wss://stream.binance.com:9443/ws/!ticker@arr');
      socket.onopen = (e: any) => {
        console.log('Socket connection established');
        this.connection = socket;
        resolve(this);
      };

      socket.onmessage = (e: any) => {
        const data = JSON.parse(e.data);
        const parsedData = data.map((i: any) => util.Binance.mapTickerSymbol(i));
        this.subscriptions.forEach(subscription => subscription(parsedData));
        Store.dispatch('TICKERS_UPDATED', parsedData);
      };

      socket.onclose = (e: any) => {
        this.connection = null;
      };

      socket.onerror = (e: any) => {
        console.error(e);
        reject(e);
      };
    });
  }

  static getInstance () {
    if (!SocketService.instance) SocketService.instance = new SocketService()
    return SocketService.instance
  }
}
