export enum DealStatus {
  BOUGHT = 'bought',
  TAKE_PROFIT_FINISHED = 'take_profit_finished',
  CANCELLED = 'cancelled',
  COMPLETED = 'completed',
  TRAILING = 'ttp_order_placed',
  TRADE_FINISHED = 'simple_trade_finished',
  PANIC_SELL_FINISHED = 'panic_sell_finished',
  FAILED = 'failed',
  STOP_LOSS_FINISHED = 'stop_loss_finished',
  TRAILING_BUY_ACTIVATED = 'trailing-buy-activated'
}

export enum TakeProfitType {
  TOTAL = 'total'
}

export enum DealCurrency {
  BASE = 'base_currency',
  QUOTE = 'quote_currency'
}

export enum DealStopLossType {
  STOP_LOSS = 'stop_loss',
  CLASSIC = 'classic'
}

export enum DealStrategyType {
  SHORT = 'short',
  LONG = 'long'
}

export interface Deal {
  id: number,
  type: string,
  bot_id: number,
  max_safety_orders: number,
  deal_has_error: boolean,
  from_currency_id: number,
  to_currency_id: number,
  account_id: number,
  active_safety_orders_count: number,
  created_at: Date,
  updated_at: Date,
  closed_at: Date | null,
  finished: boolean,
  current_active_safety_orders_count: number,
  current_active_safety_orders: number,
  completed_safety_orders_count: number,
  completed_manual_safety_orders_count: number,
  cancellable: boolean,
  panic_sellable: boolean,
  trailing_enabled: boolean,
  tsl_enabled: boolean,
  stop_loss_timeout_enabled: boolean,
  stop_loss_timeout_in_seconds: number,
  active_manual_safety_orders: number,
  pair: string,
  status: DealStatus,
  take_profit: number, // map
  base_order_volume: number, // map
  safety_order_volume: number, // map
  safety_order_step_percentage: number, // map
  bought_amount: number, // map
  bought_volume: number, // map
  bought_average_price: number, // map
  sold_amount: number, // map
  sold_volume: number, // map
  sold_average_price: number, // map
  take_profit_type: TakeProfitType,
  final_profit: number, // map
  martingale_coefficient: number, // map
  martingale_volume_coefficient: number, // map
  martingale_step_coefficient: number, // map
  stop_loss_percentage: number, // map
  error_message: string | null,
  profit_currency: DealCurrency,
  stop_loss_type: DealStopLossType,
  safety_order_volume_type: DealCurrency,
  base_order_volume_type: DealCurrency,
  from_currency: string,
  to_currency: string,
  current_price: number, // map
  take_profit_price: number, // map
  final_profit_percentage: number, // map
  actual_profit_percentage: number, // map
  bot_name: string,
  account_name: string,
  usd_final_profit: number, // map
  actual_profit: number, // map
  actual_usd_profit: number, // map
  failed_message: string | null,
  reserved_base_coin: number, // map
  reserved_second_coin: number, // map
  trailing_deviation: number, // map
  trailing_max_price: number | null, // map
  tsl_max_price: number | null, // map
  strategy: DealStrategyType
}