









































































































import { Vue, Component, Watch, Prop } from 'vue-property-decorator'
import { Portfolio, PortfolioType, AvailableExchanges, AvailableExchangeIcons, PortfolioExchangeConfiguration } from '../model/system/Portfolio'
import { v4 as uuid } from 'uuid';
import PortfoliosService from '@/services/services/PortfoliosService';
import FrameworkService from '../services/FrameworkService';
import { InbestStore } from '../io/InbestStore';
import { Inbest } from '../model/Inbest';
import { CreatePortfolioRequest } from '../model/services/portfolios/CreatePortfolio';

@Component({
  filters: {
    currencyIcon (currency: string) {
      return `https://github.com/atomiclabs/cryptocurrency-icons/raw/master/32/color/${currency.toLowerCase()}.png`
    },

    exchangeLogo (exchangeName: string) {
      return AvailableExchangeIcons[exchangeName];
    }
  }
})
export default class PortfolioCreation extends Vue {
  @Prop(Function) readonly completed!: Function;
  @Prop(Function) readonly failed!: Function;

  private readonly frameworkService: FrameworkService = FrameworkService.getInstance();

  public readonly portfolio: CreatePortfolioRequest = {
    Name: '',
    BaseCurrency: '',
    Type: PortfolioType.PAPER,
    ExchangeName: '',
    PaperTradingInitialAmount: 10000,
    ExchangeApiCredentials: {
      apiKey: '',
      apiSecret: ''
    }
  }

  get availableExchanges () {
    const exchanges = Object.values(AvailableExchanges);

    return exchanges.map(exchange => ({
      value: exchange,
      label: exchange
    }));
  }

  get baseCurrencies () {
    return [
      {
        label: 'US Dollar',
        value: 'USD'
      },
      {
        label: 'Tether',
        value: 'USDT'
      },
      {
        label: 'Binance USD',
        value: 'BUSD'
      },
      {
        label: 'Euro',
        value: 'EUR'
      },
      {
        label: 'Bitcoin',
        value: 'BTC'
      },
      {
        label: 'Ethereum',
        value: 'ETH'
      }
    ]
  }

  async created () {
  }

  async mounted () {}

  async createPortfolio () {
    if (!this.portfolio.Name) {
      this.portfolio.Name = 'Default'
    }

    try {
      const portfolioId = await this.$store.dispatch('CREATE_PORTFOLIO', this.portfolio);
      this.frameworkService.addAlert('success', 'Portfolio created successfully');
      this.completed(portfolioId);
    } catch (e) {
      console.error(e);
      this.frameworkService.addAlert('error', `Failed to create portfolio: ${e}`);
    }
  }

  resetExchange () {
    this.portfolio.ExchangeName = '';
  }

  selectPaperTrading () {
    this.portfolio.Type = PortfolioType.PAPER;
    this.resetExchange();
  }

  @Watch('portfolio.ExchangeName', { immediate: true })
  onExchangeSelection (value: string) {
    if (value && value.length) {
      this.portfolio.Type = PortfolioType.EXCHANGE;
    }
  }
  
}
