






import { Vue, Component } from 'vue-property-decorator'
import { InbestStore } from '../io/InbestStore'

@Component
export default class BotZoom extends Vue {

  get botId () {
    return parseInt(this.$route.params.botId);
  }

  get bot () {
    return (this.$store.state as InbestStore).autotrading.bots.filter(b => b.id === this.botId)[0];
  }

  async created () {}

  async mounted () {
    const botId = this.botId;
  }
}
