





























import { Vue, Component, Watch } from 'vue-property-decorator'
import Amplify from 'aws-amplify'
import { AmplifyEventBus, components as AmplifyComponents } from 'aws-amplify-vue'
import FrameworkService from './services/FrameworkService';
import Framework from '@/components/Framework.vue';
import { Inbest } from './model/Inbest';
import InbestView from '@/views/Inbest.vue'
import { InbestRoute } from './model/ui/SystemDefinitions';
import AppHeader from './components/AppHeader.vue';

@Component({
  components: {
    AppHeader,
    InbestView,
    Framework,
    ...AmplifyComponents
  }
})
export default class App extends Vue {
  private readonly fwk: FrameworkService = FrameworkService.getInstance();
  private dataUpdateSchedule = null;

  get status () {
    return (this.$store.state as Inbest).config.status;
  }

  async created () {
    AmplifyEventBus.$on('authState', async (info: string) => {
      console.log('DEBUG: Authentication data changed');
      await this.verifyAuth()
    });
    
    await this.verifyAuth();
  }
  
  async mounted () {
    this.dataUpdateSchedule = setInterval(async () => {
      // await this.$store.dispatch('REFRESH_DATA');
    }, 30000); // TODO

  }

  async updated () {
    
  }
  
  async verifyAuth (): Promise<void> {
    try {
      const loggedIn = await this.$store.dispatch('AUTHENTICATE');
      if (loggedIn) {
        await this.$store.dispatch('START_APP');
      } else {
        // Stop app
      }
    } catch (e) {
      console.error('Failed to initialise app');
      console.error(e);
    }
  }

  @Watch('$store.state.config.status', { immediate: true })
  onStatusChange () {
    this.$forceUpdate(); // TODO Change this
  }
}
