import numeral from 'numeral';

export default {
  formattedNumber (value: number, format: string = '0,0.00') {
    return numeral(value).format(format);
  },
  maxPrecision (value: number) {
    const precision = 5; // TODO
    const multiplier = Math.pow(10, precision);

    const round = value | 0;
    const decimal = value - round;
    const roundDecimals = Math.round(decimal * multiplier) / multiplier;
    return round + roundDecimals;
  },
  percent (value: number) {
    if (!isFinite(value)) return '∞';
    return Math.round(value * 100);
  },
  shortNumber (value: number) {
    const absValue = Math.abs(value);

    // Thousands
    if (absValue > 999) {
      const multiplier = value / 1000;
      const round = Math.floor(multiplier);
      const decimal = Math.floor((multiplier - round) * 10) / 10;
      return `${round + decimal}k`;
    }

    if (absValue > 99) {
      return `${value | 0}`;
    }

    return ((value * 100) | 0) / 100;
  }
}