








































































































































































































































import { Vue, Component, Prop } from 'vue-property-decorator'
import { NoCache } from '../decorators';
import { DepositStatus } from 'binance-api-node';
import { Numbers } from '../util';

@Component({
  filters: {
    maxPrecision (value: number) {
      return Numbers.maxPrecision(value);
    },
    percent (value: number) {
      return Numbers.percent(value);
    },
    shortNumber (value: number) {
      return Numbers.shortNumber(value);
    }
  }
})
export default class AssetDetailsList extends Vue {
  @Prop(String) readonly portfolioId!: string;

  @NoCache
  get portfolio (): Portfolio {
    const portfolio: Portfolio = this.$store.state.portfolios.filter((item: Portfolio) => item.id === this.portfolioId)[0];
    return portfolio;
  }

  get balances (): UiAssetBalance[] {
    const balances = (this.portfolio && this.portfolio.configuration ? this.portfolio.configuration.balances : []).filter(item => (
      parseFloat(item.free) > 0 ||
      parseFloat(item.locked) > 0
    ));
    
    const mapped = balances.map((item: any) => ({
      ...item,
      icon: `https://github.com/atomiclabs/cryptocurrency-icons/raw/master/32/color/${item.asset.toLowerCase()}.png`,
      free: parseFloat(item.free),
      locked: parseFloat(item.locked),
      total: parseFloat(item.free) + parseFloat(item.locked),
      marketValue: this.getMarketValue(item.asset)
    }));

    const total = mapped.map(item => item.total * item.marketValue).reduce((total, item) => total + item, 0);

    return mapped.map(item => ({
      ...item,
      currentMarketValue: item.total * item.marketValue,
      percentageOfHoldings: (item.total * item.marketValue) / total
    })).sort((a, b) => b.percentageOfHoldings - a.percentageOfHoldings);
    
  }

  getAssetDetails (symbol: string) {
    const asset = (this.$store.state as StoreDefinition).assets.filter(a => a.asset === symbol)[0];
    return asset;
  }

  getAssetIO (asset: string, timespan: number) {
    const deposits = this.getAssetDeposits(asset, timespan);
    const withdrawals = this.getAssetWithdrawals(asset, timespan);

    const result = deposits - withdrawals;
    return result;
  }

  getAssetDeposits (asset: string, timespan: number) {
    const details = this.getAssetDetails(asset);
    if (!details) return 0;

    const now = Date.now();
    const interestedDate = now - (timespan * 1000);
    const deposits = details.deposits
      .filter(d => d.status === BinanceDepositStatus.SUCCESS)
      .filter(d => d.insertTime > interestedDate)
      .map(d => d.amount);

    return deposits.reduce((total, item) => total + item, 0);    
  }

  getAssetWithdrawals (asset: string, timespan: number) {
    const details = this.getAssetDetails(asset);
    if (!details) return 0;

    const now = Date.now();
    const interestedDate = now - (timespan * 1000);
    const withdrawals = details.withdrawals
      .filter(d => d.status === BinanceWithdrawalStatus.COMPLETED)
      .filter(d => d.applyTime > interestedDate)
      .map(d => d.amount);

    return withdrawals.reduce((total, item) => total + item, 0);    
  }

  getAssetTrades (asset: string, timespan: number) {
    const details = this.getAssetDetails(asset);

    const now = Date.now();
    const interestedDate = now - (timespan * 1000);
    
    const response = {
      inbound: 0,
      outbound: 0
    };

    if (!details) return response;

    const markets = details.markets;
    markets.forEach(market => {
      const symbol = market.symbol;
      const marketDefinition = this.portfolio.exchangeInformation.symbols.filter(s => s.symbol === symbol)[0];      

      const orders = market.orders
        .filter(order => order.time > interestedDate)
        .filter(order => order.status === 'FILLED');
      orders.forEach(order => {
        const side = order.side;
        if (marketDefinition.quoteAsset === asset) {
          if (side === 'SELL') {
            response.inbound += parseFloat(order.cummulativeQuoteQty);
          } else {
            response.outbound += parseFloat(order.cummulativeQuoteQty);
          }
        } else {
          if (side === 'SELL') {
            response.outbound += parseFloat(order.executedQty);
          } else {
            response.inbound += parseFloat(order.executedQty);
          }
        }
      })
    });

    return response;
  }

  getAssetYield (asset, balance, timespan) {
    const orders = this.getAssetTrades(asset, timespan);

    const change = orders.inbound - orders.outbound;
    const previousBalance = balance - change;
    const percent = change / previousBalance;
    
    return {
      change,
      percent
    }
  }

  getMarketValue (symbol: string) {
    if (symbol === 'USDT') return 1;
    const bid = parseFloat((this.$store.state.tickers.filter((ticker: Ticker) => ticker.symbol === `${symbol}USDT`)[0] || {}).bestBid);
    
    if (isNaN(bid)) {
      // No USDT market, try BTC
      const bid2 = parseFloat((this.$store.state.tickers.filter((ticker: Ticker) => ticker.symbol === `${symbol}BTC`)[0] || {}).bestBid);
      const btcBid = parseFloat((this.$store.state.tickers.filter((ticker: Ticker) => ticker.symbol === `BTCUSDT`)[0] || {}).bestBid);
      return bid2 * btcBid;
    }

    return bid;
  }

  async created () {
  }

  async mounted () {
  }
}
