
import { Vue, Component, Prop, Mixins } from 'vue-property-decorator'
import { Doughnut } from 'vue-chartjs';

@Component
export default class DoughnutChart extends Mixins(Doughnut) {
  @Prop(Object) readonly data!: { [key: string]: any };
  @Prop({ required: false }) readonly attributes!: string[];
  @Prop({ required: false }) readonly options!: any;

  async created () {}

  async mounted () {
    this.update();
  }

  async updated () {
    this.update();
  }

  async update () {
    const options = {
      responsive: true,
      maintainAspectRatio: false,
      legend: {
        display: false
      },
      tooltips: {
        enabled: false
      },
      cutoutPercentage: 65
    };

    this.renderChart(this.data, this.options || options);
  }
}
