import { OperationType } from './../../model/autotrading/Trade';
import { TakeProfitType, DealCurrency, DealStrategyType, DealStopLossType } from './../../model/autotrading/Deal';
import { BotProps, BotStrategy } from './../../model/autotrading/Bot';

export default class Bot implements BotProps {

  private readonly config: BotProps;

  constructor (config: BotProps) {
    this.config = {
      ...config,
      take_profit: parseFloat((config as any).take_profit),
      base_order_volume: parseFloat((config as any).base_order_volume),
      safety_order_volume: parseFloat((config as any).safety_order_volume),
      safety_order_step_percentage: parseFloat((config as any).safety_order_step_percentage),
      martingale_volume_coefficient: parseFloat((config as any).martingale_volume_coefficient),
      martingale_step_coefficient: parseFloat((config as any).martingale_step_coefficient),
      stop_loss_percentage: parseFloat((config as any).stop_loss_percentage),
      cooldown: parseFloat((config as any).cooldown),
      min_volume_btc_24h: parseFloat((config as any).min_volume_btc_24h),
      min_price: parseFloat((config as any).min_price),
      trailing_deviation: parseFloat((config as any).trailing_deviation),
      finished_deals_profit_usd: parseFloat((config as any).finished_deals_profit_usd),
      finished_deals_count: parseFloat((config as any).finished_deals_count),
    }
  }

  get id (): number {
    return this.config.id;  
  }

  get account_id (): number {
    return this.config.account_id;  
  }

  get is_enabled (): boolean {
    return this.config.is_enabled;  
  }

  get max_safety_orders (): number {
    return this.config.max_safety_orders;  
  }

  get active_safety_orders_count (): number {
    return this.config.active_safety_orders_count;  
  }

  get pairs (): string[] {
    return this.config.pairs;  
  }

  get strategy_list (): BotStrategy[] {
    return this.config.strategy_list;  
  }

  get max_active_deals (): number {
    return this.config.max_active_deals;  
  }

  get active_deals_count (): number {
    return this.config.active_deals_count;  
  }

  get deletable (): boolean {
    return this.config.deletable;  
  }

  get created_at (): Date {
    return this.config.created_at;  
  }

  get updated_at (): Date {
    return this.config.updated_at;  
  }

  get trailing_enabled (): boolean {
    return this.config.trailing_enabled;  
  }

  get tsl_enabled (): boolean {
    return this.config.tsl_enabled;  
  }

  get deal_start_delay_seconds (): number | null {
    return this.config.deal_start_delay_seconds;  
  }

  get stop_loss_timeout_enabled (): boolean {
    return this.config.stop_loss_timeout_enabled;  
  }

  get stop_loss_timeout_in_seconds (): number {
    return this.config.stop_loss_timeout_in_seconds;  
  }

  get disable_after_deals_count (): number | null {
    return this.config.disable_after_deals_count;  
  }

  get deals_counter (): number | null {
    return this.config.deals_counter;  
  }

  get allowed_deals_on_same_pair (): number {
    return this.config.allowed_deals_on_same_pair;  
  }

  get easy_form_supported (): boolean {
    return this.config.easy_form_supported;  
  }

  get name (): string {
    return this.config.name;  
  }

  get take_profit (): number {
    return this.config.take_profit;  
  }

  get base_order_volume (): number {
    return this.config.base_order_volume;  
  }

  get safety_order_volume (): number {
    return this.config.safety_order_volume;  
  }

  get safety_order_step_percentage (): number {
    return this.config.safety_order_step_percentage;  
  }

  get take_profit_type (): TakeProfitType {
    return this.config.take_profit_type;  
  }

  get type (): string {
    return this.config.type;  
  }

  get martingale_volume_coefficient (): number {
    return this.config.martingale_volume_coefficient;  
  }

  get martingale_step_coefficient (): number {
    return this.config.martingale_step_coefficient;  
  }

  get stop_loss_percentage (): number {
    return this.config.stop_loss_percentage;  
  }

  get cooldown (): number {
    return this.config.cooldown;  
  }

  get strategy (): DealStrategyType {
    return this.config.strategy;  
  }

  get min_volume_btc_24h (): number {
    return this.config.min_volume_btc_24h;  
  }

  get profit_currency (): DealCurrency {
    return this.config.profit_currency;  
  }

  get min_price (): number {
    return this.config.min_price;  
  }

  get max_price (): number | null {
    return this.config.max_price;  
  }

  get stop_loss_type (): DealStopLossType {
    return this.config.stop_loss_type;  
  }

  get safety_order_volume_type (): DealCurrency {
    return this.config.safety_order_volume_type;  
  }

  get base_order_volume_type (): DealCurrency {
    return this.config.base_order_volume_type;  
  }

  get account_name (): string {
    return this.config.account_name;  
  }

  get trailing_deviation (): number {
    return this.config.trailing_deviation;  
  }

  get finished_deals_profit_usd (): number {
    return this.config.finished_deals_profit_usd;  
  }

  get finished_deals_count (): number {
    return this.config.finished_deals_count;  
  }

  get leverage_type (): string {
    return this.config.leverage_type;  
  }

  get leverage_custom_value (): null {
    return this.config.leverage_custom_value;  
  }

  get start_order_type (): OperationType {
    return this.config.start_order_type;  
  }

}