










import { Vue, Component } from 'vue-property-decorator'
import FrameworkService from '../services/FrameworkService';
import numeral from 'numeral';
import { InbestStore } from '../io/InbestStore';

@Component({
  filters: {
    formattedNumber (value: number) {
      return numeral(value).format('0,0')
    },
    maxPrecision (value: number) {
      const val = Math.round(value * 100) / 100;
      const str = `${val}`;
      if (str.indexOf('.') === -1) return `${str}.00`;
      if (str.indexOf('.') === str.length - 2) return `${str}0`
      return str;
    } 
  }
})
export default class BalanceOverview extends Vue {

  private readonly fwk: FrameworkService = FrameworkService.getInstance();

  get assets () {
    return (this.$store.state as InbestStore).portfolio.balances.filter(a => a.total !== 0);
  }

  get prices () {
    return (this.$store.state as InbestStore).portfolio.prices;
  }

  get totalHoldings () {
    const value = this.assets
      .map(asset => asset.total * this.getAssetPrice(asset.asset))
      .reduce((t, i) => t + i, 0);

    return value;
  }

  getAssetPrice (asset: string) {
    // TODO
    // @ts-ignore
    if (asset === 'USDT') return 1

    const symbol = `${asset}USDT`; // TODO

    if (!this.prices[symbol]) {
      const btcMarket = this.prices.BTCUSDT;
      const btcAssetMarket = this.prices[`${asset}BTC`];

      if (!btcMarket || !btcAssetMarket) {
        console.error(`Could not find symbol for asset ${asset}`);
        return 0;
      }

      // FIXME
      // @ts-ignore
      return btcMarket * btcAssetMarket;
    }

    return this.prices[symbol];
  }

  get absoluteBalance () {
    return Math.floor(this.totalHoldings);
  }

  get decimalBalance () {
    const decimals = Math.round((this.totalHoldings % this.absoluteBalance) * 100);
    if (!decimals) return '00';
    if (decimals < 10) return `${decimals}0`;
    if (decimals > 99) return `99`;
    return decimals;
  }

  async created () {
    
  }

  async mounted () {
  }
}
