import { Ticker } from './../../io/exchange/Ticker';

import { Asset } from './../../io/exchange/Asset';
import { InbestStoreExchangeConfiguration, InbestStore } from './../../io/InbestStore';
import { BinanceExchangeConfiguration, BinanceExchangeInfo } from './../../model/exchanges/binance/BinanceExchangeConfiguration';
import { TickersUpdateRequest } from '../../actions/exchange/ExchangeLiveActions';
import { InbestExchangeConfiguration } from '../../model/Inbest';

export interface ExchangeAccountDataUpdateRequest {
  portfolioId: string;
  data: BinanceExchangeConfiguration
}

export interface ExchangeInformationUpdateRequest {
  portfolioId: string;
  data: BinanceExchangeInfo;
}

export interface PriceChangeRequest {
  portfolioId: string;
  data: { [key: string]: number };
}

export default {
  UPDATE_TICKERS (state: InbestStore, data: TickersUpdateRequest) {
    const tickersMap: { [key: string]: Ticker } = data.tickers.reduce((total, ticker) => ({
      ...total,
      [ticker.symbol]: ticker
    }), {});

    let exchange: InbestExchangeConfiguration = state.exchanges[data.exchangeName];
    if (!exchange) {
      exchange = state.exchanges[data.exchangeName] = {
        exchangeName: data.exchangeName,
        tickers: tickersMap
      }
    } else {
      Object.keys(tickersMap).forEach(symbol => exchange.tickers[symbol] = tickersMap[symbol]);
    }
  }
}