








































import { Vue, Component, Prop } from 'vue-property-decorator'
import { InbestStore } from '../io/InbestStore'
import Asset from '@/components/AssetOverview.vue';
import SocketService from '../services/SocketService';
import { Ticker } from '../io/exchange/Ticker';
import FrameworkService from '../services/FrameworkService';
import { AssetsVisibilityMode } from '../model/ui/SystemDefinitions';

@Component({
  components: {
    Asset
  }
})
export default class AssetOverviewList extends Vue {
  @Prop(String) readonly mode!: AssetsVisibilityMode;

  private readonly fwk: FrameworkService = FrameworkService.getInstance();
  private readonly socket: SocketService = SocketService.getInstance();

  get assets () {
    return (this.$store.state as InbestStore).portfolio.balances.filter(a => a.total !== 0);
  }

  get marketTickers () {
    return (this.$store.state as InbestStore).portfolio.prices;
  }

  get sortedAssets () {
    const assets = this.assets;
    const totalHoldings = this.totalHoldings;

    return this.assets.sort((a, b) => 
      b.total * (this.getAssetPrice(b.asset) || 0) - 
      a.total * (this.getAssetPrice(a.asset) || 0)
    )
  }

  get totalHoldings () {
    return this.assets
      .map(asset => asset.total * (this.getAssetPrice(asset.asset) || 0))
      .reduce((t, i) => t + i, 0);
  }

  getAssetPrice (asset: string) {
    // TODO
    // @ts-ignore
    if (asset === 'USDT') return 1

    const symbol = `${asset}USDT`; // TODO

    if (!this.marketTickers[symbol]) {
      const btcMarket = this.marketTickers.BTCUSDT;
      const btcAssetMarket = this.marketTickers[`${asset}BTC`];

      if (!btcMarket || !btcAssetMarket) {
        // throw new Error(`Could not find symbol for asset ${asset}`)
        return undefined;
      }

      // FIXME
      // @ts-ignore
      return btcMarket * btcAssetMarket;
    }

    return this.marketTickers[symbol];
  }
  
  openAssetDetails () {
    this.$router.push('/balances');
  }

  async created () {
    
  }

  async mounted () {}
}
