import Bot from '@/io/autotrading/Bot';
import Trade from '@/io/autotrading/Trade';
import Deal from '@/io/autotrading/Deal';
import { InbestStore } from '@/io/InbestStore';

export default {
  UPDATE_BOTS (state: InbestStore, bots: Bot[]) {
    state.autotrading.bots = bots;
  },

  UPDATE_DEALS (state: InbestStore, deals: Deal[]) {
    state.autotrading.deals = deals;
  },

  UPDATE_TRADES (state: InbestStore, trades: Trade[]) {
    state.autotrading.trades = trades;
  }
}