

























import { Vue, Component, Prop } from 'vue-property-decorator'
import FrameworkService from '../services/FrameworkService';
import * as util from '../util';
import { ExtendedAsset } from '../model/Inbest';
import { InbestStore } from '../io/InbestStore';
import numeral from 'numeral';
import { Ticker } from '../io/exchange/Ticker';
import SocketService from '../services/SocketService';

@Component({
  filters: {
    number (value: number) {
      return numeral(value).format('0,0.00');
    }
  }
})
export default class MarketOverview extends Vue {
  @Prop(Boolean) readonly mini!: boolean;

  private readonly fwk: FrameworkService = FrameworkService.getInstance();
  private readonly socket: SocketService = SocketService.getInstance();
  public tickers: { [symbol: string]: Ticker } | null = null;

  private totalHoldings = 0;
  private usdMarkets = [];

  get state () {
    return (this.$store.state as InbestStore);
  }

  get myTopUsdtMarkets () {
    const markets = this.usdMarkets;

    const ret = markets.map(market => {
      const ticker = this.tickers[market];
      if (!ticker) {
        return null
      }

      return {
        market,
        value: ticker.bestBid,
        change: ticker.priceChangePercent
      }
    });

    return ret
      .filter(r => !!r)
      .filter(t => t.value > 0.009)
      .slice(0, 10);
  }

  get assets () {
    return this.state.portfolio.balances;
  }

  get sortedAssets () {
    return this.assets.sort((a, b) => (b.total * this.getAssetPrice(b.asset)) > (a.total * this.getAssetPrice(a.asset)) ? 1 : -1)
  }

  get prices () {
    return this.state.portfolio!.prices;
  }

  getAssetPrice (asset: string) {
    // TODO
    // @ts-ignore
    if (asset === 'USDT') return 1

    const symbol = `${asset}USDT`; // TODO

    if (!this.prices[symbol]) {
      const btcMarket = this.prices.BTCUSDT;
      const btcAssetMarket = this.prices[`${asset}BTC`];

      if (!btcMarket || !btcAssetMarket) {
        // throw new Error(`Could not find symbol for asset ${asset}`)
        return undefined;
      }

      // FIXME
      // @ts-ignore
      return btcMarket * btcAssetMarket;
    }

    return this.prices[symbol];
  }

  async created () {
    this.socket.subscribe((data) => {
      this.tickers = {
        ...this.tickers,
        ...data.reduce((t, i) => ({ ...t, [i.symbol]: i }))
      };
    });

    await this.socket.connect();
  }

  async mounted () {
    const sortedAssets = this.sortedAssets.filter(a => a.asset !== 'USDT').slice(0, 15);
    this.usdMarkets = sortedAssets.map(a => `${a.asset}USDT`);
  }
}
