import ExchangeOperation from '@/io/base/ExchangeOperation';

export enum TradeStrategy {
  LONG = 'long',
  SHORT = 'short',
  UNKNOWN = 'unknown'
}

export enum TradeStatus {
  AWAITING = 'awaiting', 
  EXECUTING = 'bought', 
  FINISHED = 'completed', 
  CANCELLED = 'cancelled',
  TRAILING = 'trailing',
  FAILED = 'failed'
}

export interface TradingCircleProps {
  quadrants: TradingCircleQuadrant[];
}

export interface TradingCircleQuadrant {
  title: string;
  href: string;
  data: TradingCircleDataItem[];
}

export interface TradingCircleDataItem extends ExchangeOperation {
  operationStrategy: TradeStrategy;
  operationStatus: TradeStatus;
  operationBaseCurrency: string;
  operationBaseAmount: number;
  operationQuoteCurrency: string;
  operationQuoteAmount: number;
  operationStartDate: Date;
  operationStartValue: number;
  operationEndDate?: Date;
  operationEndValue?: number;
  operationTargetValue?: number;
  operationTrailingValue?: number;
}