import InbestDataService from '@/services/InbestDataService';
import PortfoliosService from '../../services/services/PortfoliosService';
import { CreatePortfolioRequest } from '../../model/services/portfolios/CreatePortfolio';
import { Portfolio } from '../../model/services/portfolios/Portfolios';

const inbestDataService = InbestDataService.getInstance();

export default {
  
  async REQUEST_AVAILABLE_PORTFOLIOS (context: any) {
    const portfoliosService = PortfoliosService.getInstance();
    const data = await portfoliosService.listPortfolios();
    const portfolios = Object.values(data);
    const ids = portfolios.map(item => item.PortfolioId);
    if (portfolios.length) {
      context.commit('UPDATE_AVAILABLE_PORTFOLIO_IDS', ids);
      context.commit('UPDATE_AVAILABLE_PORTFOLIOS', portfolios);
    }
    
    return data;
  },

  async CREATE_PORTFOLIO (context: any, portfolio: CreatePortfolioRequest): Promise<string> {
    const portfoliosService = PortfoliosService.getInstance();

    const request: CreatePortfolioRequest = {
      ...portfolio,
      ExchangeName: portfolio.Type === 'paper' ? 'paper' : portfolio.ExchangeName
    }

    try {
      const portfolioId = await portfoliosService.createPortfolio(request);
      return portfolioId;
    } catch (e) {
      throw new Error(e);
    }

  }
} 