import Binance from 'binance-api-node';
import Store from '@/store';

export default class BinanceService {
  private static instance: BinanceService;

  private readonly client: any;
  private connected = false;

  constructor () {
    this.client = Binance();
  }

  get api () {
    return this.client;
  }

  async startTickerSubscription () {
    if (this.connected) return;
    this.connected = true;
    return await new Promise((resolve, reject) => {
      const socket = new WebSocket('wss://stream.binance.com:9443/ws/!ticker@arr');
      socket.onopen = (e: any) => {
        console.log('Socket connection established');
        resolve(this);
      };

      socket.onmessage = (e: any) => {
        const data = JSON.parse(e.data);
        Store.dispatch('RAW_TICKERS_UPDATED', { exchangeName: 'binance', data });
      };

      socket.onerror = (e: any) => {
        console.error(e);
        reject(e);
      };
    });
  }

  static getInstance () {
    if (!BinanceService.instance) BinanceService.instance = new BinanceService()
    return BinanceService.instance
  }
}
