














import { Vue, Component } from 'vue-property-decorator'
import DoughnutChart from '@/components/graphs/DoughnutChart.vue';
import AssetDefinitions, { AssetTypeColors, AssetType } from '../model/ui/AssetDefinitions';
import { InbestStore } from '../io/InbestStore';
import FrameworkService from '../services/FrameworkService';
import { Asset } from '../io/exchange/Asset';
import randomcolor from 'randomcolor';
import * as util from '../util';

@Component({
  components: {
    DoughnutChart
  }
})
export default class AssetHealth extends Vue {
  private readonly fwk = FrameworkService.getInstance();

  public readonly assetMarketHoldings: { [asset: string]: number } = {}
  
  public chartVolatilityData = null;
  public chartDistributionData = null;
  public chartAvailabilityData = null;

  private extendedAssets = null

  get chartOptions () {
    return {
      responsive: true,
      maintainAspectRatio: false,
      legend: {
        display: false
      },
      cutoutPercentage: 80
    }
  }

  get availabilityData () {
    const assets = this.extendedAssets;
    const mapped = assets.reduce((t, i) => ({
      free: (t.free || 0) + i.$freeMarket,
      locked: (t.locked || 0) + i.$lockedMarket
    }), {});

    const dataForChart: any[] = Object.values(mapped).sort((a: any, b: any) => a.market - b.market);

    const chartData = {
      labels: ['Free', 'Locked'],
      datasets: [{
        backgroundColor: ['#5db494', 'rgb(243, 193, 99)'],
        borderColor: 'transparent',
        data: [
          Math.round((mapped.free / this.totalHoldings) * 100),
          Math.round((mapped.locked / this.totalHoldings) * 100)
        ]
      }]
    }

    return chartData;
  }

  get distributionData () {
    const assets = this.extendedAssets.filter(a => a.$marketValue > 0);
    const mapped = assets.reduce((t, i) => ({
      ...t,
      [i.asset]: {
        ...i,
        market: i.$marketValue,
        percentage: i.$percentage,
      }
    }), {});

    const dataForChart: any[] = Object.values(mapped).sort((a: any, b: any) => a.market - b.market);

    const chartData = {
      labels: dataForChart.map(i => i.asset),
      datasets: [{
        backgroundColor: dataForChart.map(i => AssetDefinitions[i.asset] ? AssetDefinitions[i.asset].color : randomcolor({
          luminosity: 'bright',
          format: 'rgb' // e.g. 'rgb(225,200,20)'
        })),
        borderColor: 'transparent',
        data: dataForChart.map((i: any) => Math.round(i.percentage * 100))
      }]
    }

    return chartData;
  }

  get volatilityData () {
    const assets = this.extendedAssets;
    const mapped = assets.reduce((t, i) => ({
      ...t,
      [i.$assetType]: {
        market: ((t[i.$assetType] || {}).market || 0) + i.$marketValue,
        percentage: ((t[i.$assetType] || {}).percentage || 0) + i.$percentage,
      }
    }), {});

    const chartData = {
      labels: ['Fiat', 'Stable', 'Volatile'],
      datasets: [{
        backgroundColor: [
          AssetTypeColors.FIAT,
          AssetTypeColors.STABLE,
          AssetTypeColors.VOLATILE
        ],
        borderColor: 'transparent',
        data: [
          Math.round(mapped['fiat'].percentage * 100),
          Math.round(mapped['stable'].percentage * 100),
          Math.round(mapped['volatile'].percentage * 100)
        ]
      }]
    }

    return chartData;
  }

  get totalHoldings () {
    const value = this.assets
      .map(asset => asset.total * this.getAssetPrice(asset.asset))
      .reduce((t, i) => t + i, 0);

    return value;
  }

  getAssetPrice (asset: string) {
    // TODO
    // @ts-ignore
    if (asset === 'USDT') return 1

    const symbol = `${asset}USDT`; // TODO

    if (!this.prices[symbol]) {
      const btcMarket = this.prices.BTCUSDT;
      const btcAssetMarket = this.prices[`${asset}BTC`];

      if (!btcMarket || !btcAssetMarket) {
        console.error(`Could not find symbol for asset ${asset}`);
        return 0;
      }

      // FIXME
      // @ts-ignore
      return btcMarket * btcAssetMarket;
    }

    return this.prices[symbol];
  }

  get state () {
    return (this.$store.state as InbestStore);
  }

  get assets () {
    return this.state.portfolio.balances;
  }

  get prices () {
    return this.state.portfolio!.prices;
  }

  async created () {
    this.extendedAssets = util.Binance.getExtendedAssets(this.totalHoldings);
  }

  async mounted () {
    setInterval(() => {
      this.chartAvailabilityData = this.availabilityData;
      this.chartVolatilityData = this.volatilityData;
      this.chartDistributionData = this.distributionData;
    }, 5000);
  }
}
