import { BaseDataWithHistory } from './../base/BaseDataWithHistory';
import { BinanceAssetBalance } from './../../model/exchanges/binance/BinanceExchangeAssets';
import { BaseAssetBalance } from './../../model/exchanges/ExchangeAssets';

export class Asset extends BaseDataWithHistory<BaseAssetBalance> implements BaseAssetBalance {

  constructor (data: BinanceAssetBalance, history?: Asset[]) {
    const parsedData: BaseAssetBalance = {
      asset: data.asset,
      available: parseFloat(data.free),
      locked: parseFloat(data.locked)
    };
    
    super(parsedData, history);
  }

  get asset () {
    return this.data.asset;
  }

  get available () {
    return this.data.available;
  }

  get locked () {
    return this.data.locked;
  }

  get total () {
    return this.available + this.locked;
  }
}