




































import { InbestStore } from '../io/InbestStore'
import moment from 'moment';
import numeral from 'numeral';
import { Vue, Component, Prop, Watch } from 'vue-property-decorator'
import Bot from '../io/autotrading/Bot';
import { Deal, DealStatus } from '../model/autotrading/Deal';
import { AssetTypeColors } from '../model/ui/AssetDefinitions';
import randomcolor from 'randomcolor';
import ExchangeOperation from '../io/base/ExchangeOperation';
import BotDetailStats from '@/components/BotDetailStats.vue';
import BotDetailBanner from '@/components/BotDetailBanner.vue';

export enum BotDetailsType {
  DASHBOARD     = 'dashboard',
  DEALS     = 'deals',
  SETTINGS  = 'settings'
}

@Component({
  components: {
    BotDetailBanner,
    BotDetailStats
  },
  filters: {
    fromNow (value: Date) {
      return moment(value).fromNow();
    }
  }
})
export default class BotDetails extends Vue {
  @Prop(Bot) readonly bot!: Bot;

  @Watch('bot', { immediate: true })
  onBotChanged () {
    this.$forceUpdate()
  }

  public currentData = BotDetailsType.DASHBOARD;
  public currentDealsPage = 0;
  public currentSection: string = 'stats'

  async created () {}

  async mounted () {}
}
