
import { Vue, Component, Prop, Mixins } from 'vue-property-decorator'
import { Bar } from 'vue-chartjs';

@Component
export default class BarChart extends Mixins(Bar) {
  @Prop(Object) readonly data!: any;
  @Prop(String) readonly attribute!: string;
  @Prop(Object) readonly options!: any[];
  @Prop(Object) readonly chartOptions: any;

  async created () {}

  async mounted () {
    this.update();
  }

  async updated () {
    this.update();
  }

  async update () {
    const options = {
      responsive: true,
      maintainAspectRatio: false
    };

    const chartData = this.data;
    this.renderChart(chartData, this.options || options);
  }
}
