






































import { Vue, Component, Prop } from 'vue-property-decorator'
import Bot from '../io/autotrading/Bot';
import { InbestStore } from '../io/InbestStore';
import moment from 'moment';
import numeral from 'numeral';

@Component({
  filters: {
    currencyValue (value: number) {
      return numeral(value).format('0,0.00');
    },
    fromNow (value: Date) {
      return moment(value).fromNow();
    }
  }
})
export default class BotList extends Vue {
  @Prop(Bot) readonly bot!: Bot;
  @Prop(Function) readonly botSelection!: Function;

  get state () {
    return (this.$store.state as InbestStore);
  }

  get bots () {
    return this.state.autotrading.bots;
  }

  get sortedBots () {
    return this.bots.sort((a, b) => b.finished_deals_profit_usd > a.finished_deals_profit_usd ? 1 : -1);
  }

  async created () {}

  async mounted () {}
}
