import apiClient from 'aws-api-gateway-client';
import ConfigurationService from '../ConfigurationService';
import Amplify from 'aws-amplify';
import { CreatePortfolioRequest, CreatePortfolioResponseSucceeded, CreatePortfolioResponseFailed } from '../../model/services/portfolios/CreatePortfolio';
import { PortfolioList } from '@/model/services/portfolios/Portfolios';

export default class PortfoliosService {
  
  private static instance: PortfoliosService;

  private readonly config: ConfigurationService;
  private apiClient?: any;

  constructor () {
    this.config = ConfigurationService.getInstance();
  }
  
  async init () {
    const credentials = await Amplify.Auth.currentCredentials();
    if (!this.apiClient) {
      this.apiClient = apiClient.newClient({
        invokeUrl: this.config.get('PORTFOLIOS_SERVICE_API_ENDPOINT'),
        region: this.config.get('AWS_REGION'),
        accessKey: credentials.accessKeyId,
        secretKey: credentials.secretAccessKey,
        sessionToken: credentials.sessionToken
      });
    }
  }

  async createPortfolio (request: CreatePortfolioRequest): Promise<string> {
    await PortfoliosService.instance.init();
    const ret = await this.apiClient.invokeApi({}, 'portfolios', 'POST', {}, request);
    const data = ret.data;
    if (data.success) {
      const response = data as CreatePortfolioResponseSucceeded;
      return response.PortfolioId;
    } else {
      const err = data as CreatePortfolioResponseFailed;
      throw new Error(err.error);
    }
  }

  async listPortfolios (): Promise<PortfolioList> {
    await PortfoliosService.instance.init();
    const ret = await this.apiClient.invokeApi({}, 'portfolios', 'GET', {});
    const data = ret.data as PortfolioList;
    return data;
  }

  static getInstance () {
    if (!PortfoliosService.instance) PortfoliosService.instance = new PortfoliosService()
    return PortfoliosService.instance
  }
}
