import { TradeStatus, TradeStrategy } from './../../model/ui/TradingCircle';


export default abstract class ExchangeOperation {
  
  abstract get operationStrategy (): TradeStrategy;
  abstract get operationStatus (): TradeStatus;
  abstract get operationBaseCurrency (): string;
  abstract get operationBaseAmount (): number;
  abstract get operationQuoteCurrency (): string;
  abstract get operationQuoteAmount (): number;
  abstract get operationCurrentProfit (): number;
  abstract get operationTargetProfit (): number;
  abstract get operationStartDate (): Date;
  abstract get operationStartValue (): number;
  abstract get operationEndDate (): Date;
  abstract get operationEndValue (): number;
  abstract get operationTargetValue (): number;
  abstract get operationTrailingValue (): number;

}