































import { Vue, Component, Watch } from 'vue-property-decorator'
import { Ticker } from '../io/exchange/Ticker';
import { Inbest } from '../model/Inbest';
import numeral from 'numeral';
import { NoCache } from '../decorators';

@Component({
  filters: {
    tickerPrice (ticker: Ticker) {
      return numeral(ticker.bestBid).format('0,0.00');
    } 
  }
})
export default class AppHeader extends Vue {

  public appMode: string = 'Basic';

  get submenu () {
    return this.$route.meta.submenu;
  }

  @NoCache
  get tickers () {
    // FIXME Change this
    const state = (this.$store.state as Inbest);
    const exchange = state.exchanges.binance;
    if (!exchange) return {};
    return exchange.tickers;
  }

  async created () {}

  async mounted () {
    setInterval(() => this.$forceUpdate(), 5000);
  }
}
