import Amplify from 'aws-amplify';
import ConfigurationService from './ConfigurationService';
import apiFactory from 'aws-api-gateway-client';


export default class ThirdPartyApiService {
  private static instance: ThirdPartyApiService;
  
  public static readonly GENERIC_API_EXCEPTION = new Error('An API error occurred');
  
  private readonly config: ConfigurationService = ConfigurationService.getInstance();

  private client: any;

  constructor () {
    
  }

  private async configure () {
    const apiEndpoint = this.config.get('THIRD_PARTY_API_ENDPOINT');
    const region = this.config.get('AWS_REGION');

    const credentials = await Amplify.Auth.currentUserCredentials();
    this.client = apiFactory.newClient({
      invokeUrl: apiEndpoint,
      region,
      accessKey: credentials.accessKeyId,
      secretKey: credentials.secretAccessKey,
      sessionToken: credentials.sessionToken
    });
  }

  private async request (method: string, path: string, params: any, additionalParams?: any, body?: any) {
    if (!this.client) await this.configure();
    
    try {
      const response = await this.client.invokeApi(params, path, method, additionalParams, body);
      // TODO Transform?
      return response.data;
    } catch (e) {
      console.error(e);
      throw ThirdPartyApiService.GENERIC_API_EXCEPTION;
    }
  }

  private async get (path: string, params: any, additionalParams?: any, body?: any) {
    return await this.request('GET', path, params, additionalParams, body);
  }

  async autotradingBots (portfolioId: string) {
    return await this.get('{portfolioId}/autotrading/bots',
      {
        portfolioId
      });
  }

  async autotradingDeals (portfolioId: string) {
    return await this.get('{portfolioId}/autotrading/deals',
      {
        portfolioId
      });
  }

  async autotradingTrades (portfolioId: string) {
    return await this.get('{portfolioId}/autotrading/trades',
      {
        portfolioId
      });
  }

  async accountInfo (portfolioId: string) {
    return await this.get('{portfolioId}/exchange/account', 
      {
        portfolioId
      });
  }

  async exchangeInfo (portfolioId: string) {
    return await this.get('{portfolioId}/exchange',
      {
        portfolioId
      });
  }

  async deposits (portfolioId: string, assetId: string) {
    return await this.get('{portfolioId}/deposits/{assetId}', 
      {
        portfolioId,
        assetId
      });
  }

  async withdrawals (portfolioId: string, assetId: string) {
    return await this.get('{portfolioId}/withdrawals/{assetId}', 
      {
        portfolioId,
        assetId
      });
  }

  async trades (portfolioId: string, symbols?: string[]) {
    return await this.get('{portfolioId}/trades', 
      {
        portfolioId,
        symbols
      });
  }

  async orders (portfolioId: string, symbols?: string[]) {
    return await this.get('{portfolioId}/orders', 
      {
        portfolioId,
        symbols
      });
  }

  async prices (portfolioId: string) {
    return await this.get('{portfolioId}/exchange/prices', 
      {
        portfolioId
      });
  }

  static getInstance () {
    if (!ThirdPartyApiService.instance) ThirdPartyApiService.instance = new ThirdPartyApiService()
    return ThirdPartyApiService.instance
  }
}
