









import { Vue, Component } from 'vue-property-decorator'
import { InbestStore } from '../io/InbestStore'

@Component
export default class ExchangeOverview extends Vue {

  async created () {}

  async mounted () {}

  get portfolio () {
    return (this.$store.state as InbestStore).portfolio;
  }

  get portfolioInfo () {
    const portfolioId = this.portfolio.portfolioId;
    const match = (this.$store.state as InbestStore).system.portfolios.filter(portfolio => portfolio.PortfolioId === portfolioId)[0];

    return match || { Name: 'Combined' };
  }
}
