import { TradeStrategy, TradeStatus } from './../../model/ui/TradingCircle';
import { DealStatus, TakeProfitType, DealCurrency, DealStopLossType, DealStrategyType } from './../../model/autotrading/Deal';
import { Deal as DealProps } from '../../model/autotrading/Deal';
import ExchangeOperation from '../base/ExchangeOperation';

export default class Deal extends ExchangeOperation implements DealProps {

  private readonly data: DealProps;

  constructor (data: DealProps) {
    super()
    this.data = {
      ...data,
      take_profit: parseFloat(data.take_profit as any),
      base_order_volume: parseFloat(data.base_order_volume as any),
      safety_order_volume: parseFloat(data.safety_order_volume as any),
      safety_order_step_percentage: parseFloat(data.safety_order_step_percentage as any),
      bought_amount: parseFloat(data.bought_amount as any),
      bought_volume: parseFloat(data.bought_volume as any),
      bought_average_price: parseFloat(data.bought_average_price as any),
      sold_amount: parseFloat(data.sold_amount as any),
      sold_volume: parseFloat(data.sold_volume as any),
      sold_average_price: parseFloat(data.sold_average_price as any),
      final_profit: parseFloat(data.final_profit as any),
      martingale_coefficient: parseFloat(data.martingale_coefficient as any),
      martingale_volume_coefficient: parseFloat(data.martingale_volume_coefficient as any),
      martingale_step_coefficient: parseFloat(data.martingale_step_coefficient as any),
      stop_loss_percentage: parseFloat(data.stop_loss_percentage as any),
      current_price: parseFloat(data.current_price as any),
      take_profit_price: parseFloat(data.take_profit_price as any),
      final_profit_percentage: parseFloat(data.final_profit_percentage as any),
      actual_profit_percentage: parseFloat(data.actual_profit_percentage as any),
      usd_final_profit: parseFloat(data.usd_final_profit as any),
      actual_profit: parseFloat(data.actual_profit as any),
      actual_usd_profit: parseFloat(data.actual_usd_profit as any),
      reserved_base_coin: parseFloat(data.reserved_base_coin as any),
      reserved_second_coin: parseFloat(data.reserved_second_coin as any),
      trailing_deviation: parseFloat(data.trailing_deviation as any),
      trailing_max_price: parseFloat(data.trailing_max_price as any),
      tsl_max_price: parseFloat(data.tsl_max_price as any),
    }
  }

  /*
   * ExchangeOperation props
   */
  get operationStrategy (): TradeStrategy {
    return (this.strategy as any) as TradeStrategy
  }

  get operationStatus (): TradeStatus {
    return (this.status as any) as TradeStatus;
  }

  get operationBaseCurrency (): string {
    return this.pair.split('_')[0]
  }

  get operationBaseAmount (): number {
    return this.bought_volume;
  }

  get operationQuoteCurrency (): string {
    return this.pair.split('_')[1]
  }

  get operationQuoteAmount (): number {
    return this.bought_amount;
  }

  get operationCurrentProfit (): number {
    return this.actual_profit_percentage;
  }

  get operationTargetProfit (): number {
    return this.take_profit;
  }

  get operationStartDate (): Date {
    return new Date(this.created_at);
  }

  get operationStartValue (): number {
    return this.bought_average_price;
  }

  get operationEndDate (): Date {
    return new Date(this.closed_at!);
  }

  get operationEndValue (): number {
    return this.sold_average_price;
  }

  get operationTargetValue (): number {
    return this.take_profit_price
  }

  get operationTrailingValue (): number {
    return 1;
  }


  /*
   * Autogenerated props
   */

  get id (): number {
    return this.data.id;
  }

  get type (): string {
    return this.data.type;
  }

  get bot_id (): number {
    return this.data.bot_id;
  }

  get max_safety_orders (): number {
    return this.data.max_safety_orders;
  }

  get deal_has_error (): boolean {
    return this.data.deal_has_error;
  }

  get from_currency_id (): number {
    return this.data.from_currency_id;
  }

  get to_currency_id (): number {
    return this.data.to_currency_id;
  }

  get account_id (): number {
    return this.data.account_id;
  }

  get active_safety_orders_count (): number {
    return this.data.active_safety_orders_count;
  }

  get created_at (): Date {
    return this.data.created_at;
  }

  get updated_at (): Date {
    return this.data.updated_at;
  }

  get closed_at (): Date | null {
    return this.data.closed_at;
  }

  get finished (): boolean {
    return this.data.finished;
  }

  get current_active_safety_orders_count (): number {
    return this.data.current_active_safety_orders_count;
  }

  get current_active_safety_orders (): number {
    return this.data.current_active_safety_orders;
  }

  get completed_safety_orders_count (): number {
    return this.data.completed_safety_orders_count;
  }

  get completed_manual_safety_orders_count (): number {
    return this.data.completed_manual_safety_orders_count;
  }

  get cancellable (): boolean {
    return this.data.cancellable;
  }

  get panic_sellable (): boolean {
    return this.data.panic_sellable;
  }

  get trailing_enabled (): boolean {
    return this.data.trailing_enabled;
  }

  get tsl_enabled (): boolean {
    return this.data.tsl_enabled;
  }

  get stop_loss_timeout_enabled (): boolean {
    return this.data.stop_loss_timeout_enabled;
  }

  get stop_loss_timeout_in_seconds (): number {
    return this.data.stop_loss_timeout_in_seconds;
  }

  get active_manual_safety_orders (): number {
    return this.data.active_manual_safety_orders;
  }

  get pair (): string {
    return this.data.pair;
  }

  get status (): DealStatus {
    return this.data.status;
  }

  get take_profit (): number {
    return this.data.take_profit;
  }

  get base_order_volume (): number {
    return this.data.base_order_volume;
  }

  get safety_order_volume (): number {
    return this.data.safety_order_volume;
  }

  get safety_order_step_percentage (): number {
    return this.data.safety_order_step_percentage;
  }

  get bought_amount (): number {
    return this.data.bought_amount;
  }

  get bought_volume (): number {
    return this.data.bought_volume;
  }

  get bought_average_price (): number {
    return this.data.bought_average_price;
  }

  get sold_amount (): number {
    return this.data.sold_amount;
  }

  get sold_volume (): number {
    return this.data.sold_volume;
  }

  get sold_average_price (): number {
    return this.data.sold_average_price;
  }

  get take_profit_type (): TakeProfitType {
    return this.data.take_profit_type;
  }

  get final_profit (): number {
    return this.data.final_profit;
  }

  get martingale_coefficient (): number {
    return this.data.martingale_coefficient;
  }

  get martingale_volume_coefficient (): number {
    return this.data.martingale_volume_coefficient;
  }

  get martingale_step_coefficient (): number {
    return this.data.martingale_step_coefficient;
  }

  get stop_loss_percentage (): number {
    return this.data.stop_loss_percentage;
  }

  get error_message (): string | null {
    return this.data.error_message;
  }

  get profit_currency (): DealCurrency {
    return this.data.profit_currency;
  }

  get stop_loss_type (): DealStopLossType {
    return this.data.stop_loss_type;
  }

  get safety_order_volume_type (): DealCurrency {
    return this.data.safety_order_volume_type;
  }

  get base_order_volume_type (): DealCurrency {
    return this.data.base_order_volume_type;
  }

  get from_currency (): string {
    return this.data.from_currency;
  }

  get to_currency (): string {
    return this.data.to_currency;
  }

  get current_price (): number {
    return this.data.current_price;
  }

  get take_profit_price (): number {
    return this.data.take_profit_price;
  }

  get final_profit_percentage (): number {
    return this.data.final_profit_percentage;
  }

  get actual_profit_percentage (): number {
    return this.data.actual_profit_percentage;
  }

  get bot_name (): string {
    return this.data.bot_name;
  }

  get account_name (): string {
    return this.data.account_name;
  }

  get usd_final_profit (): number {
    return this.data.usd_final_profit;
  }

  get actual_profit (): number {
    return this.data.actual_profit;
  }

  get actual_usd_profit (): number {
    return this.data.actual_usd_profit;
  }

  get failed_message (): string | null {
    return this.data.failed_message;
  }

  get reserved_base_coin (): number {
    return this.data.reserved_base_coin;
  }

  get reserved_second_coin (): number {
    return this.data.reserved_second_coin;
  }

  get trailing_deviation (): number {
    return this.data.trailing_deviation;
  }

  get trailing_max_price (): number | null {
    return this.data.trailing_max_price;
  }

  get tsl_max_price (): number | null {
    return this.data.tsl_max_price;
  }

  get strategy (): DealStrategyType {
    return this.data.strategy;
  }
}