








import { Vue, Component } from 'vue-property-decorator'

export interface MenuItem {
  title: string;
  action: Function;
  icon: string;
}

@Component
export default class AppMenu extends Vue {

  get menuItems () {
    const that = this;
    return [
      {
        title: 'Home',
        action () { that.$router.push('/') },
        icon: 'fas fa-tachometer-alt'
      },
      {
        title: 'Trading',
        action () { that.$router.push('trading') },
        icon: 'fas fa-chart-line'
      },      
      {
        title: 'Intelligence',
        action () { that.$router.push('intelligence') },
        icon: 'fas fa-brain'
      }
    ];
  }

  async created () {}

  async mounted () {}
}
